import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "../css/AccountSettings.css";
import { changePassword, deleteAccount, logout, updateMember, getCurrentMember, getStoredMember } from "../utils/api";
import SignupTermsPopup from "../components/SignupTermsPopup";

function AccountSettings() {
  const navigate = useNavigate();

  const [currentPw, setCurrentPw] = useState("");
  const [newPw, setNewPw] = useState("");
  const [confirmPw, setConfirmPw] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");

  const [deletePw, setDeletePw] = useState("");
  const [deleteError, setDeleteError] = useState("");
  const [isDeleting, setIsDeleting] = useState(false);

  //  
  const [deliveryInfo, setDeliveryInfo] = useState({
    memHp: "",
    memZipcode: "",
    memAddress1: "",
    memAddress2: "",
  });
  const [isUpdatingDelivery, setIsUpdatingDelivery] = useState(false);
  const [deliveryError, setDeliveryError] = useState("");

  //   
  const [showTermsPopup, setShowTermsPopup] = useState(false);
  const [termsType, setTermsType] = useState(null);

  //   
  useEffect(() => {
    const loadDeliveryInfo = async () => {
      try {
        const member = await getCurrentMember();
        setDeliveryInfo({
          memHp: member.memHp || "",
          memZipcode: member.memZipcode || "",
          memAddress1: member.memAddress1 || "",
          memAddress2: member.memAddress2 || "",
        });
      } catch (error) {
        //     localStorage 
        const member = getStoredMember();
        if (member) {
          setDeliveryInfo({
            memHp: member.memHp || "",
            memZipcode: member.memZipcode || "",
            memAddress1: member.memAddress1 || "",
            memAddress2: member.memAddress2 || "",
          });
        }
      }
    };
    loadDeliveryInfo();
  }, []);

  //   (  API)
  const handleAddressSearch = () => {
    if (window.daum && window.daum.Postcode) {
      new window.daum.Postcode({
        oncomplete: function(data) {
          let addr = '';
          
          if (data.userSelectedType === 'R') {
            addr = data.roadAddress;
          } else {
            addr = data.jibunAddress;
          }

          setDeliveryInfo(prev => ({
            ...prev,
            memZipcode: data.zonecode,
            memAddress1: addr
          }));
          
          document.getElementById('delivery-address2')?.focus();
        }
      }).open();
    } else {
      alert('     .  .');
    }
  };

  //   
  const handleDeliveryChange = (e) => {
    const { name, value } = e.target;
    setDeliveryInfo(prev => ({
      ...prev,
      [name]: value
    }));
    setDeliveryError("");
  };

  //  
  const handleUpdateDelivery = async () => {
    setDeliveryError("");

    //  
    if (!deliveryInfo.memHp || !deliveryInfo.memHp.trim()) {
      setDeliveryError(" .");
      return;
    }

    if (!deliveryInfo.memZipcode || !deliveryInfo.memAddress1) {
      setDeliveryError(" .");
      return;
    }

    setIsUpdatingDelivery(true);
    try {
      const updatedMember = await updateMember({
        memHp: deliveryInfo.memHp,
        memZipcode: deliveryInfo.memZipcode,
        memAddress1: deliveryInfo.memAddress1,
        memAddress2: deliveryInfo.memAddress2 || "",
      });
      
      alert("  .");
      
      // localStorage 
      const currentMember = getStoredMember();
      if (currentMember) {
        const updatedData = {
          ...currentMember,
          memHp: updatedMember.memHp,
          memZipcode: updatedMember.memZipcode,
          memAddress1: updatedMember.memAddress1,
          memAddress2: updatedMember.memAddress2,
        };
        localStorage.setItem('member', JSON.stringify(updatedData));
        window.dispatchEvent(new Event('loginStatusChanged'));
      }
    } catch (error) {
      setDeliveryError(error.message || "  .");
    } finally {
      setIsUpdatingDelivery(false);
    }
  };

  const handlePasswordChange = async () => {
    setError("");

    //  
    if (!currentPw || !newPw || !confirmPw) {
      setError("  .");
      return;
    }

    if (newPw.length < 8) {
      setError("  8  .");
      return;
    }

    if (newPw !== confirmPw) {
      setError("   .");
      return;
    }

    if (currentPw === newPw) {
      setError("     .");
      return;
    }

    setIsLoading(true);
    try {
      await changePassword(currentPw, newPw);
      alert("  .");
      setCurrentPw("");
      setNewPw("");
      setConfirmPw("");
      setError("");
    } catch (error) {
      setError(error.message || "  .");
    } finally {
      setIsLoading(false);
    }
  };

  const handleDeleteAccount = async () => {
    setDeleteError("");

    if (!deletePw) {
      setDeleteError("  .");
      return;
    }

    if (!window.confirm("  ?     .")) {
      return;
    }

    setIsDeleting(true);
    try {
      await deleteAccount(deletePw);
      alert(" .");
      logout();
      navigate("/");
    } catch (error) {
      setDeleteError(error.message || "  .");
    } finally {
      setIsDeleting(false);
    }
  };

  return (
    <div className="account-settings-container">
      <button className="back-btn" onClick={() => navigate("/mypage")}>
          
      </button>

      <h2 className="page-title"> </h2>

      {/*   */}
      <section className="setting-section">
        <h3> </h3>
        <p className="section-desc">
                 
        </p>

        <div className="input-group">
          <input
            type="text"
            name="memHp"
            placeholder="  (: 010-1234-5678)"
            value={deliveryInfo.memHp}
            onChange={handleDeliveryChange}
            disabled={isUpdatingDelivery}
          />
          <div className="input-with-button">
            <input
              type="text"
              name="memZipcode"
              placeholder=""
              value={deliveryInfo.memZipcode}
              onChange={handleDeliveryChange}
              readOnly
              disabled={isUpdatingDelivery}
            />
            <button
              type="button"
              className="check-button"
              onClick={handleAddressSearch}
              disabled={isUpdatingDelivery}
            >
              
            </button>
          </div>
          <input
            type="text"
            name="memAddress1"
            placeholder=""
            value={deliveryInfo.memAddress1}
            onChange={handleDeliveryChange}
            readOnly
            disabled={isUpdatingDelivery}
          />
          <input
            type="text"
            id="delivery-address2"
            name="memAddress2"
            placeholder=" "
            value={deliveryInfo.memAddress2}
            onChange={handleDeliveryChange}
            disabled={isUpdatingDelivery}
          />
        </div>

        {deliveryError && (
          <div className="error-message">
            {deliveryError}
          </div>
        )}

        <button 
          className="change-btn" 
          onClick={handleUpdateDelivery}
          disabled={isUpdatingDelivery}
        >
          {isUpdatingDelivery ? " ..." : " "}
        </button>
      </section>

      {/*   */}
      <section className="setting-section">
        <h3> </h3>
        <p className="section-desc">
               
        </p>

        <div className="input-group">
          <input
            type="password"
            placeholder="  "
            value={currentPw}
            onChange={(e) => {
              setCurrentPw(e.target.value);
              setError("");
            }}
            disabled={isLoading}
          />
          <input
            type="password"
            placeholder="  ( 8 )"
            value={newPw}
            onChange={(e) => {
              setNewPw(e.target.value);
              setError("");
            }}
            disabled={isLoading}
          />
          <input
            type="password"
            placeholder="   "
            value={confirmPw}
            onChange={(e) => {
              setConfirmPw(e.target.value);
              setError("");
            }}
            disabled={isLoading}
          />
        </div>

        {error && (
          <div className="error-message">
            {error}
          </div>
        )}

        <button 
          className="change-btn" 
          onClick={handlePasswordChange}
          disabled={isLoading}
        >
          {isLoading ? " ..." : " "}
        </button>
      </section>

      {/*   */}
      <section className="setting-section">
        <h3> </h3>
        <p className="section-desc">    </p>

        <div className="privacy-btns">
          <button onClick={() => {
            setTermsType('privacy');
            setShowTermsPopup(true);
          }}>
             
          </button>
          <button onClick={() => {
            setTermsType('service');
            setShowTermsPopup(true);
          }}>
            
          </button>
        </div>
      </section>

      {/*   */}
      <section className="setting-section danger">
        <h3> </h3>
        <p className="section-desc">
               
        </p>
        <div className="input-group">
          <input
            type="password"
            placeholder="  "
            value={deletePw}
            onChange={(e) => {
              setDeletePw(e.target.value);
              setDeleteError("");
            }}
            disabled={isDeleting}
          />
        </div>
        {deleteError && (
          <div className="error-message">
            {deleteError}
          </div>
        )}
        <button
          className="delete-btn"
          onClick={handleDeleteAccount}
          disabled={isDeleting}
        >
          {isDeleting ? " ..." : " "}
        </button>
      </section>

      {/*   */}
      {showTermsPopup && (
        <SignupTermsPopup
          type={termsType}
          onClose={() => {
            setShowTermsPopup(false);
            setTermsType(null);
          }}
        />
      )}
    </div>
  );
}

export default AccountSettings;