import React, { useState, useEffect } from 'react';
import TagCheckboxGroup from './TagCheckboxGroup';
import '../../css/admin/ProductForm.css';

/**
 * []      
 */

const organizeCategories = (categories) => {
  const parents = categories.filter(c => !c.parentCategory && !c.parentCategoryNo);
  const result = [];

  parents.forEach(parent => {
    result.push({ ...parent, displayName: parent.categoryName, isParent: true });
    const children = categories.filter(c => {
      const pId = c.parentCategory ? c.parentCategory.categoryNo : c.parentCategoryNo;
      return pId === parent.categoryNo;
    });
    children.forEach(child => {
      result.push({ ...child, displayName: ` ${child.categoryName}`, isParent: false });
    });
  });
  return result;
};

const TAG_OPTIONS = {
  skinTypes: [
    { id: 'dry', label: '' }, { id: 'oily', label: '' },
    { id: 'combination', label: '' }, { id: 'sensitive', label: '' }
  ],
  skinConcerns: [
    { id: 'hydration', label: '' }, { id: 'moisture', label: '' },
    { id: 'brightening', label: '' }, { id: 'tone', label: '' },
    { id: 'soothing', label: '' }, { id: 'sensitive', label: '' },
    { id: 'uv', label: '' }, { id: 'wrinkle', label: '' },
    { id: 'elasticity', label: '' }, { id: 'pores', label: '' }
  ],
  personalColors: [
    { id: 'spring', label: ' ' },
    { id: 'summer', label: ' ' },
    { id: 'autumn', label: ' ' },
    { id: 'winter', label: ' ' }
  ]
};

function ProductForm({ initialData, categories, onSubmit, isEdit }) {
  //     
  const [formData, setFormData] = useState({
    prdName: '', description: '', categoryNo: '', prdPrice: 0,
    status: 'SALE', howToUse: '', skinType: [], skinConcern: [], personalColor: [],
    ...initialData
  });

  //     (: 1)
  const [options, setOptions] = useState(initialData?.options || [
    { optionName: '', optionValue: '', addPrice: 0, stock: 0 }
  ]);

  /**
   *    State
   * - type: OLD( URL)  NEW(  File )
   * - url:    URL
   */
  const [imageList, setImageList] = useState([]);

  //     State 
  useEffect(() => {
    if (initialData) {
      setFormData(prev => ({
        ...prev,
        ...initialData,
        skinType: initialData.skinTypes || [],
        skinConcern: initialData.skinConcerns || [],
        personalColor: initialData.personalColors || []
      }));
      if (initialData.options && initialData.options.length > 0) {
        setOptions(initialData.options);
      }
      //   URL    
      if (initialData.imageUrls && initialData.imageUrls.length > 0) {
        const oldImages = initialData.imageUrls.map(url => ({
          type: 'OLD',
          url: url,
          file: null
        }));
        setImageList(oldImages);
      }
    }
  }, [initialData]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleFileChange = (e) => {
    if (e.target.files && e.target.files.length > 0) {
      const newFiles = Array.from(e.target.files).map(file => ({
        type: 'NEW',
        url: URL.createObjectURL(file), //  Blob URL
        file: file
      }));
      //    
      setImageList(prev => [...prev, ...newFiles]);
    }
    e.target.value = ''; //    
  };

  //   
  const removeImage = (index) => {
    setImageList(prev => prev.filter((_, i) => i !== index));
  };

  //    (  )
  const setAsMainImage = (index) => {
    if (index === 0) return;
    setImageList(prev => {
      const list = [...prev];
      const item = list.splice(index, 1)[0];
      list.unshift(item); //   
      return list;
    });
  };

  const handleCheckboxChange = (groupName, value) => {
    setFormData(prev => {
      const current = prev[groupName] || [];
      const next = current.includes(value)
        ? current.filter(item => item !== value)
        : [...current, value];
      return { ...prev, [groupName]: next };
    });
  };
  const handleOptionChange = (index, field, value) => {
    const newOptions = [...options];
    newOptions[index][field] = value;
    setOptions(newOptions);
  };

  const addOption = () => {
    setOptions([...options, { optionName: '', optionValue: '', addPrice: 0, stock: 0 }]);
  };

  const removeOption = (index) => {
    if (options.length === 1) return alert(" 1  .");
    setOptions(options.filter((_, i) => i !== index));
  };

  /**
   *   
   * -     URL      
   * -  (onSubmit)   
   */
  const handleSubmit = (e) => {
    e.preventDefault();

    //          ("NEW_FILE")   
    const imageOrderList = imageList.map(img =>
      img.type === 'OLD' ? img.url : "NEW_FILE"
    );

    //      
    const newFiles = imageList
      .filter(img => img.type === 'NEW')
      .map(img => img.file);

    const finalData = { ...formData, keptImageUrls: imageOrderList };

    onSubmit(finalData, options, newFiles);
  };

  const sortedCategories = organizeCategories(categories || []); // categories undefined  

  return (
    <>
      <div className="form-header">
        <h2 className="form-page-title">{isEdit ? `  (ID: ${initialData.prdNo})` : '  '}</h2>
        <button className="btn-submit" onClick={handleSubmit}>
          {isEdit ? ' ' : ' '}
        </button>
      </div>

      <form className="product-form-container" onSubmit={handleSubmit}>
        {/*   */}
        <div>
          <div className="form-section">
            <h3 className="section-title"> </h3>
            <div className="form-group">
              <label className="form-label"> *</label>
              <input className="form-input" name="prdName" value={formData.prdName} onChange={handleChange} required placeholder=" " />
            </div>
            <div className="form-group">
              <label className="form-label"> </label>
              <textarea className="form-textarea" name="description" value={formData.description} onChange={handleChange} placeholder="  " />
            </div>
            <div className="form-group">
              <label className="form-label"> </label>
              <textarea className="form-textarea" style={{ minHeight: '100px' }} name="howToUse" value={formData.howToUse} onChange={handleChange} placeholder="  " />
            </div>
          </div>

          <div className="form-section">
            <h3 className="section-title">   </h3>
            <div className="option-header">
              <span style={{ width: '20%' }}></span>
              <span style={{ flex: 1 }}></span>
              <span style={{ width: '15%' }}></span>
              <span style={{ width: '15%' }}></span>
              <span style={{ width: '50px' }}></span>
            </div>
            {options.map((opt, idx) => (
              <div className="option-row" key={idx}>
                <input className="form-input" style={{ width: '20%' }} placeholder=": " value={opt.optionName} onChange={e => handleOptionChange(idx, 'optionName', e.target.value)} />
                <input className="form-input" style={{ flex: 1 }} placeholder=": 50ml" value={opt.optionValue} onChange={e => handleOptionChange(idx, 'optionValue', e.target.value)} />
                <input className="form-input" type="number" style={{ width: '15%' }} placeholder="0" value={opt.addPrice} onChange={e => handleOptionChange(idx, 'addPrice', e.target.value)} />
                <input className="form-input" type="number" style={{ width: '15%' }} placeholder="0" value={opt.stock} onChange={e => handleOptionChange(idx, 'stock', e.target.value)} />
                <button type="button" className="btn-remove-option" onClick={() => removeOption(idx)}>X</button>
              </div>
            ))}
            <button type="button" className="btn-add-option" onClick={addOption}>+  </button>
          </div>

          <div className="form-section">
            <h3 className="section-title">  ()</h3>
            <TagCheckboxGroup label=" " groupName="skinType" options={TAG_OPTIONS.skinTypes} selectedValues={formData.skinType} onChange={handleCheckboxChange} />
            <TagCheckboxGroup label=" " groupName="skinConcern" options={TAG_OPTIONS.skinConcerns} selectedValues={formData.skinConcern} onChange={handleCheckboxChange} />
            <TagCheckboxGroup label=" " groupName="personalColor" options={TAG_OPTIONS.personalColors} selectedValues={formData.personalColor} onChange={handleCheckboxChange} />
          </div>
        </div>

        {/*   */}
        <div>
          <div className="form-section">
            <h3 className="section-title"></h3>
            <div className="form-group">
              <label className="form-label"> </label>
              <select className="form-select" name="status" value={formData.status} onChange={handleChange}>
                <option value="SALE"></option>
                <option value="SOLD_OUT"></option>
                <option value="STOP"></option>
              </select>
            </div>
            <div className="form-group">
              <label className="form-label"> *</label>
              <select className="form-select" name="categoryNo" value={formData.categoryNo} onChange={handleChange} required>
                <option value="" disabled> </option>
                {sortedCategories.map(cat => (
                  <option
                    key={cat.categoryNo}
                    value={cat.categoryNo}
                    disabled={cat.isParent}
                    style={cat.isParent ? { fontWeight: 'bold', color: '#333' } : { color: '#555' }}
                  >
                    {cat.displayName}
                  </option>
                ))}
              </select>
            </div>
            <div className="form-group">
              <label className="form-label">  *</label>
              <input className="form-input" type="number" name="prdPrice" value={formData.prdPrice} onChange={handleChange} required />
            </div>
          </div>

          {/*    */}
          <div className="form-section">
            <h3 className="section-title"> </h3>

            {/*    */}
            {imageList.length > 0 ? (
              <div className="image-upload-container">
                {imageList.map((img, idx) => (
                  <div
                    key={idx}
                    className={`image-preview-card ${idx === 0 ? 'is-main' : ''}`}
                    onClick={() => setAsMainImage(idx)}
                    title="   "
                  >
                    <img
                      src={img.url}
                      alt={`img-${idx}`}
                      onError={(e) => {
                        e.target.onerror = null;
                        e.target.src = '/prd_placeholder.png';
                      }} />
                    {idx === 0 && <div className="main-badge"></div>}
                    {img.type === 'NEW' && <div className="new-badge">NEW</div>} {/*    */}
                    <button
                      type="button"
                      className="btn-remove-image"
                      onClick={(e) => { e.stopPropagation(); removeImage(idx); }}
                    ></button>
                  </div>
                ))}
              </div>
            ) : (
              <div className="no-image">  </div>
            )}

            <input
              type="file"
              accept="image/*"
              multiple /*    */
              onChange={handleFileChange}
              style={{ fontSize: '14px' }}
            />
            <p className="image-help-text">
              *   <strong> </strong> .<br />
              *       .<br />
              * 1:1 (1000px) 
            </p>
          </div>
        </div>
      </form>
    </>
  );
}

export default ProductForm;