import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import axios from 'axios';
import { API_BASE_URL } from '../../utils/api';
import ProductForm from '../../components/admin/ProductForm';

function AdminProductNew() {
  const navigate = useNavigate();
  const [categories, setCategories] = useState([]);

  useEffect(() => {
    //   (Axios)
    axios.get(`${API_BASE_URL}/categories`)
      .then(res => setCategories(res.data))
      .catch(() => toast.error('  '));
  }, []);

  const handleCreate = async (formData, options, imageFiles) => {
    //  
    if (!formData.prdName) return toast.warn(' .');
    if (!formData.categoryNo) return toast.warn(' .');

    // DTO 
    const productDto = {
      ...formData,
      categoryNo: Number(formData.categoryNo),
      prdPrice: Number(formData.prdPrice),
      stock: 0,
      skinType: formData.skinType.join(','),
      skinConcern: formData.skinConcern.join(','),
      personalColor: formData.personalColor.join(','),
      options: options.map(opt => ({
        ...opt,
        addPrice: Number(opt.addPrice),
        stock: Number(opt.stock)
      }))
    };

    const dataToSend = new FormData();
    dataToSend.append("dto", new Blob([JSON.stringify(productDto)], { type: "application/json" }));

    //   FormData 
    if (imageFiles && imageFiles.length > 0) {
      imageFiles.forEach(file => {
        dataToSend.append("imageFiles", file); //  List<MultipartFile> 
      });
    }

    try {
      //  
      const token = localStorage.getItem('token');

      // Axios POST
      await axios.post(`${API_BASE_URL}/admin/products`, dataToSend, {
        headers: {
          'Content-Type': 'multipart/form-data',
          'Authorization': `Bearer ${token}`
        }
      });

      toast.success(' .');
      navigate('/admin/products');
    } catch (error) {
      console.error(error);
      toast.error('    .');
    }
  };

  return (
    <ProductForm
      categories={categories}
      onSubmit={handleCreate}
      isEdit={false}
    />
  );
}

export default AdminProductNew;