import React, { useState } from 'react';
import '../css/NoticePage.css';

//   
const NOTICE_DATA = [
  {
    id: 1,
    title: '[]       ',
    date: '2025-11-01',
    content: `, Coco.\n\nCoco       2,000    .\n  .\n\n: 2025.11.01 ~   `
  },
  {
    id: 2,
    title: '[]       ',
    date: '2025-12-01',
    content: `, Coco.\n\n          .\n\n-  : 12 23()  2    \n-  : 12 26()  \n\n      1~2   ,\n      .\n\n   !`
  },
  {
    id: 3,
    title: '[]    (12/05 02:00 ~ 04:00)',
    date: '2025-11-30',
    content: `        .\n      .\n\n: 2025 12 5  2 ~ 4 (2)`
  },
  {
    id: 4,
    title: '[]     ',
    date: '2025-11-20',
    content: `     3  5,000  !\n\n:     ( )`
  }
];

function NoticePage() {
  //     (null  )
  const [openId, setOpenId] = useState(null);

  const toggleNotice = (id) => {
    setOpenId(openId === id ? null : id);
  };

  return (
    <div className="notice-container">
      <div className="notice-header">
        <h1></h1>
        <p>Coco    .</p>
      </div>

      <div className="notice-list">
        {/*  */}
        <div className="notice-list-header">
          <span className="col-id">No</span>
          <span className="col-title"></span>
          <span className="col-date"></span>
        </div>

        {/*   */}
        {NOTICE_DATA.sort((a, b) => b.id - a.id).map((notice) => (
          <div key={notice.id} className={`notice-item ${openId === notice.id ? 'active' : ''}`}>

            {/*   ( ) */}
            <div className="notice-summary" onClick={() => toggleNotice(notice.id)}>
              <span className="col-id">{notice.id}</span>
              <span className="col-title">{notice.title}</span>
              <span className="col-date">{notice.date}</span>

              {/* SVG   */}
              <div className="arrow-icon">
                <svg
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                >
                  {/*     */}
                  <polyline points="6 9 12 15 18 9"></polyline>
                </svg>
              </div>
            </div>

            {/*   () */}
            {openId === notice.id && (
              <div className="notice-content">
                <p>{notice.content}</p>
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}

export default NoticePage;