import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';
import Pagination from '../../components/admin/Pagination';
import Spinner from '../../components/admin/Spinner';
import PointUpdateModal from '../../components/admin/PointUpdateModal';
import { fetchWithAuth } from '../../utils/api';
import '../../css/admin/AdminProductList.css'; 
import '../../css/admin/AdminComponents.css';
import editIcon from '../../images/edit.svg';

const LIMIT = 10;

function AdminMemberList() {
  const [members, setMembers] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  //  
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedRole, setSelectedRole] = useState('ALL');
  const [selectedLoginType, setSelectedLoginType] = useState('ALL');

  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);

  //  
  const [dashboardStats, setDashboardStats] = useState({
    totalMembers: 0,
    adminCount: 0,
    userCount: 0,
    normalCount: 0,
    socialCount: 0,
    kakaoCount: 0,
    naverCount: 0,
    googleCount: 0
  });

  //    
  const [selectedMember, setSelectedMember] = useState(null); //    
  const [isModalOpen, setIsModalOpen] = useState(false);

  const loadMembers = useCallback(async () => {
    setIsLoading(true);
    try {
      const params = new URLSearchParams({
        page: currentPage.toString(),
        size: LIMIT.toString(),
      });

      if (searchTerm) {
        params.append('searchTerm', searchTerm);
      }
      if (selectedRole && selectedRole !== 'ALL') {
        params.append('role', selectedRole);
      }

      const response = await fetchWithAuth(`/member/admin/list?${params.toString()}`);
      const data = await response.json();

      if (response.ok) {
        let filteredMembers = data.members || [];
        
        //    ()
        if (selectedLoginType && selectedLoginType !== 'ALL') {
          filteredMembers = filteredMembers.filter(member => 
            member.loginType === selectedLoginType
          );
        }
        
        setMembers(filteredMembers);
        setTotalPages(data.totalPages || 0);
        setDashboardStats(data.stats || {
          totalMembers: 0,
          adminCount: 0,
          userCount: 0,
          normalCount: 0,
          socialCount: 0,
          kakaoCount: 0,
          naverCount: 0,
          googleCount: 0
        });
      } else {
        throw new Error(data.message || '   .');
      }
    } catch (error) {
      console.error("   :", error);
      toast.error(error.message || "    .");
    }
    setIsLoading(false);
  }, [currentPage, searchTerm, selectedRole, selectedLoginType]);

  // useEffect loadMembers 
  useEffect(() => {
    loadMembers();
  }, [loadMembers]);

  const handleSearch = (e) => {
    setSearchTerm(e.target.value);
    setCurrentPage(1);
  };

  //  
  const handleOpenModal = (member) => {
    setSelectedMember(member);
    setIsModalOpen(true);
  };

  //  
  const handleCloseModal = () => {
    setIsModalOpen(false);
    setSelectedMember(null);
  };

  //      
  const handleUpdateSuccess = () => {
    loadMembers(); //  
  };

  const formatDate = (dateString) => {
    if (!dateString) return '-';
    try {
      const date = new Date(dateString);
      return date.toLocaleDateString('ko-KR', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
      });
    } catch (e) {
      return dateString;
    }
  };

  if (isLoading) {
    return <Spinner />;
  }

  return (
    <div className="admin-page-container">
      <h2 className="page-title"> </h2>
      {/* ---  --- */}
      <div className="dashboard-container">
          <div className="dash-card">
            <p className="dash-title"> </p>
            <p className="dash-value">{dashboardStats.totalMembers}</p>
          </div>
        <div className="dash-card">
          <p className="dash-title"> </p>
          <p className="dash-value">{dashboardStats.normalCount}</p>
        </div>
        <div className="dash-card">
          <p className="dash-title"> </p>
          <p className="dash-value">{dashboardStats.socialCount}</p>
        </div>
        <div className="dash-card">
          <p className="dash-title"></p>
          <p className="dash-value">{dashboardStats.adminCount}</p>
        </div>
        <div className="dash-card">
          <p className="dash-title"> </p>
          <p className="dash-value">{currentPage} / {totalPages || 1}</p>
        </div>
      </div>

      {/* ---   --- */}
      <div className="admin-content-card">
        <div className="content-header">
          <h3> </h3>
          <button className="btn-refresh" onClick={loadMembers}> </button>
        </div>

        {/*  /  */}
        <div className="filter-container">
          <select 
            className="filter-select"
            value={selectedRole}
            onChange={(e) => { setSelectedRole(e.target.value); setCurrentPage(1); }}
          >
            <option value="ALL"> </option>
            <option value="USER"> </option>
            <option value="ADMIN"></option>
          </select>

          <select 
            className="filter-select"
            value={selectedLoginType}
            onChange={(e) => { setSelectedLoginType(e.target.value); setCurrentPage(1); }}
          >
            <option value="ALL">  </option>
            <option value=""> </option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>

          <input
            type="text"
            className="search-input"
            placeholder=", , ,  ..."
            value={searchTerm}
            onChange={handleSearch}
          />
        </div>

        {/* ---   --- */}
        <div className="table-wrapper">
          <table className="admin-table">
            <thead>
              <tr>
                <th style={{width: '50px'}}>No</th>
                <th style={{width: '60px'}}></th>
                <th style={{width: '90px'}}></th>
                <th style={{width: '80px'}}></th>
                <th style={{width: '80px'}}></th>
                <th style={{width: '100px'}}></th>
                <th style={{width: '80px'}}> </th>
                <th style={{width: '80px'}}></th>
                <th style={{width: '80px'}}></th>
                <th style={{width: '80px'}}></th>
              </tr>
            </thead>
            <tbody>
              {isLoading ? (
                <tr><td colSpan="10" className="loading-cell"><Spinner /></td></tr>
              ) : members.length > 0 ? (
                members.map((member) => (
                  <tr key={member.memNo}>
                    <td>{member.memNo}</td>
                    <td style={{maxWidth: '150px'}}> {/*  td    */}
                        <div 
                            className="fw-bold" 
                            title={member.memId} //       
                            style={{
                                whiteSpace: 'nowrap', 
                                overflow: 'hidden', 
                                textOverflow: 'ellipsis',
                                cursor: 'help' //   
                            }}
                        >
                            {member.memId}
                        </div>
                    </td>
                    <td>{member.memNickname}</td>
                    <td>{member.memName}</td>
                    <td style={{maxWidth: '200px'}}>
                        <div 
                            title={member.memMail}
                            style={{
                                whiteSpace: 'nowrap', 
                                overflow: 'hidden', 
                                textOverflow: 'ellipsis',
                                cursor: 'help' //   
                            }}
                        >
                            {member.memMail}
                        </div>
                    </td>
                    <td>{member.memHp}</td>
                    <td>
                      <span className={`status-tag`} 
                            style={{
                              backgroundColor: 
                                member.loginType === '' ? '#FEE500' :
                                member.loginType === '' ? '#03C75A' :
                                member.loginType === '' ? '#4285F4' :
                                '#6c757d',
                              color: member.loginType === '' ? '#000' : '#fff',
                              fontSize: '11px'
                              }}>
                        {member.loginType || ''}
                      </span>
                    </td>
                    <td>
                      <span className={`status-tag`} 
                            style={{
                              backgroundColor: member.role === 'ADMIN' ? '#dc3545' : '#28a745',
                              fontSize: '11px'
                              }}>
                        {member.role === 'ADMIN' ? '' : ''}
                      </span>
                    </td>

                    {/*         */}
                    <td style={{whiteSpace: 'nowrap'}}>
                        <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center', gap: '5px'}}>
                            <span>{member.point?.toLocaleString()} P</span>
                            <button 
                                className="icon-btn edit" 
                                onClick={() => handleOpenModal(member)} 
                                title=" "
                            >
                                <img src={editIcon} alt="" />
                            </button>
                        </div>
                    </td>

                    <td>{formatDate(member.memJoindate)}</td>
                  </tr>
                ))
              ) : (
                <tr><td colSpan="10" className="empty-cell">  .</td></tr>
              )}
            </tbody>
          </table>
        </div>

        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={setCurrentPage}
        />
      </div>
    
    {/*     */}
      <PointUpdateModal 
        isOpen={isModalOpen}
        onClose={handleCloseModal}
        member={selectedMember}
        onSuccess={handleUpdateSuccess}
      />

    </div>
  );
}

export default AdminMemberList;

