import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import ReviewDetail from "./ReviewDetail";
import axios from "axios";
import "../css/reviewButton.css";

function ProductReviews({ productNo }) {

    const [reviews, setReviews] = useState([]);
    const [loading, setLoading] = useState(true);
    const [orderItemNo, setOrderItemNo] = useState(0);
    const navigate = useNavigate();

    const [page, setPage] = useState(0);
    const [totalPages, setTotalPages] = useState(0);
    const [sortType, setSortType] = useState("latest");
    const [coMate, setCoMate] = useState(false);        //  
    const pageSize = 10;

    //  
    const handleDeleteReview = async (reviewNo) => {
        try {
            const token = localStorage.getItem('token');
            const headers = {};

            if (token) {
                headers['Authorization'] = `Bearer ${token}`;
            }

            await axios.delete(`http://13.231.28.89:18080/api/reviews/${reviewNo}`, { headers });
            setReviews(currentReviews =>
                currentReviews.filter(review => review.reviewNo !== reviewNo))
        } catch (error) {
            console.error("  :", error);
            alert("    .");
        }
    };

    //    
    useEffect(() => {
        const fetchReviews = async () => {
            setLoading(true);
            try {
                const token = localStorage.getItem('token');
                const headers = {};
                if (coMate) { // co-mate     
                    if (!token) {
                        alert("Co-mate   .");
                        setLoading(false);
                        setCoMate(false);
                        return;
                    }
                    headers["Authorization"] = `Bearer ${token}`;
                }
                const response = await axios.get(`http://13.231.28.89:18080/api/products/${productNo}/reviewPages`, {
                    params: {
                        page,
                        size: pageSize,
                        sortType, // latest / oldest
                        coMate,
                    },
                    headers,
                });
                setReviews(response.data.content);
                setTotalPages(response.data.totalPages);
            } catch (error) {
                console.error("   :", error);
            } finally {
                setLoading(false);
            }

        };

        fetchReviews();
    }, [productNo, page, sortType, coMate]);

    //   
    const getOrerItemNo = async () => {
        setLoading(true);
        try {
            const token = localStorage.getItem("token");
            if (!token) {
                alert(" .");
                return;
            }
            const response = await axios.get(`http://13.231.28.89:18080/api/reviews/${productNo}/getOrderItemNo`, { headers: { Authorization: `Bearer ${token}` } });
            const orderItemNoFromApi = response.data;
            setOrderItemNo(orderItemNoFromApi);
            return navigate(`/reviews/${orderItemNoFromApi}`);
        } catch (error) {
            console.log("orderItemNo   .", error);
            const msg = error.response?.data?.message
                || "    .";
            alert(msg);
        } finally {
            setLoading(false);
        }
    }

    if (loading) {
        return <div style={{ padding: '20px', textAlign: 'center' }}>  ...</div>;
    }

    return (
        <div className="review-list-container" style={{ maxWidth: '1100px', margin: '0 auto' }}>
            <div className="review-header">
                <h2 className="review-title"> ( {reviews.length})</h2>
                <div className="filter-container">
                    <button type="button" className="filter-latest" onClick={() => {
                        setSortType("latest");
                        setPage(0);
                    }}></button>
                    <p className="filter-bar"> | </p>
                    <button type="button" className="filter-oldest" onClick={() => {
                        setSortType("oldest");
                        setPage(0);
                    }}> </button>
                    <p className="filter-bar"> | </p>
                    <label className="filter-co-mate">
                        <input
                            type="checkbox"
                            checked={coMate}
                            onChange={(e) => {
                                setCoMate(e.target.checked);
                                setPage(0);
                            }}
                        />
                        Co-mate 
                    </label>
                </div>
                <button
                    className="review-btn"
                    onClick={() => getOrerItemNo()}
                > </button>
            </div>
            {reviews.length === 0 ? (
                <div className="error">  .</div>
            ) : null}
            {reviews.map((review) => (
                <ReviewDetail
                    key={review.reviewNo}
                    reviewData={review}
                    onDelete={handleDeleteReview}
                    productNo={productNo}
                />
            ))}

            <div className="pagination" style={{ textAlign: "center", margin: "20px 0" }}>
                <button
                    disabled={page === 0}
                    onClick={() => setPage((p) => p - 1)}
                >
                    
                </button>
                <span style={{ margin: "0 10px" }}>
                    {page + 1} / {totalPages || 1}
                </span>
                <button
                    disabled={page + 1 >= totalPages}
                    onClick={() => setPage((p) => p + 1)}
                >
                    
                </button>
            </div>
        </div>
    );
}

export default ProductReviews;