import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import "../css/ProfileEdit.css";
import { getStoredMember, getAuthHeaders } from "../utils/api";
import axios from "axios";

function ProfileEdit() {
  const navigate = useNavigate();

  //  
  const member = getStoredMember();
  const memNo = member?.memNo;

  //  
  const [nickname, setNickname] = useState(member?.memNickname || "");
  const [email] = useState(member?.memMail || "");

  // 
  const [skinType, setSkinType] = useState("");
  const [concerns, setConcerns] = useState([]);
  const [personalColor, setPersonalColor] = useState("");

  //   
  useEffect(() => {
    if (!memNo) return;

    const headers = getAuthHeaders();
    axios
      .get(`http://13.231.28.89:18080/api/coco/members/profile/${memNo}`, { headers })
      .then((res) => {
        const data = res.data;
        console.log("  :", data);

        setSkinType(data.skinType || "");
        setPersonalColor(data.personalColor || "");

        if (Array.isArray(data.concerns)) {
          setConcerns(data.concerns);
        }
      })
      .catch((err) => {
        console.error("  :", err);
        if (err.response?.status === 403) {
          alert("    .");
        } else if (err.response?.status === 401) {
          alert(" .");
        }
      });
  }, [memNo]);

  //  
  const handleConcernChange = (e) => {
    const value = e.target.value;
    setConcerns((prev) =>
      prev.includes(value) ? prev.filter((v) => v !== value) : [...prev, value]
    );
  };

  // 
  const handleSave = () => {
    if (!memNo) {
      alert(" !");
      return;
    }

    const requestBody = {
      skinType,
      concerns,
      personalColor,
    };

    const headers = getAuthHeaders();
    axios
      .put(
        `http://13.231.28.89:18080/api/coco/members/profile/${memNo}`,
        requestBody,
        { headers }
      )
      .then(() => {
        alert("  !");
        navigate("/mypage");
      })
      .catch((err) => {
        console.error("  :", err);
        if (err.response?.status === 403) {
          alert("    .");
        } else if (err.response?.status === 401) {
          alert(" .");
        } else {
          alert("   : " + (err.response?.data || err.message));
        }
      });
  };

  return (
    <div className="profile-edit-container">
      {/*  */}
      <button className="back-btn" onClick={() => navigate("/mypage")}>
        <svg
          className="arrow-icon"
          width="14"
          height="14"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
        >
          <line x1="19" y1="12" x2="5"  y2="12"></line>
          <polyline points="12 19 5 12 12 5"></polyline>
        </svg>
      <span> </span></button>

      <h2 className="page-title"> </h2>

      {/*   */}
      <div className="section">
        <h3> </h3>

        <label></label>
        <div className="input-row">
          <input
            type="text"
            className="input readonly"
            value={nickname} 
            readOnly
          />
        </div>

        <label> (ID)</label>
        <input type="email" className="input readonly" value={email} readOnly />
      </div>

      {/*   */}
      <div className="section">
        <h3> </h3>
        <p className="section-desc">
            <b>Filter by My Profile</b>  Co-mate  .
        </p>

        {/*   */}
        <div className="sub-section">
          <h4> </h4>
          <div className="option-grid">
            {["", "", "", ""].map((type) => (
              <label key={type}>
                <input
                  type="radio"
                  name="skinType"
                  value={type}
                  checked={skinType === type}
                  onChange={(e) => setSkinType(e.target.value)}
                />
                {type}
              </label>
            ))}
          </div>
        </div>

        {/*   */}
        <div className="sub-section">
          <h4>  (  )</h4>
          <div className="option-grid checkbox">
            {[
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              ""
            ].map((item) => (
              <label key={item}>
                <input
                  type="checkbox"
                  value={item}
                  checked={concerns.includes(item)}
                  onChange={handleConcernChange}
                />
                {item}
              </label>
            ))}
          </div>
        </div>

        {/*   */}
        <div className="sub-section">
          <h4> </h4>
          <select
            className="select"
            value={personalColor}
            onChange={(e) => setPersonalColor(e.target.value)}
          >
            <option value=""></option>
            <option> </option>
            <option> </option>
            <option> </option>
            <option> </option>
          </select>
        </div>
      </div>

      {/*   */}
      <button className="save-btn" onClick={handleSave}>
         
      </button>
    </div>
  );
}

export default ProfileEdit;