import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../css/FindAccount.css';
import BackIcon from '../images/back.svg';
import { 
  sendFindIdVerificationCode, 
  findId, 
  sendResetPasswordVerificationCode, 
  resetPassword,
  validateEmail
} from '../utils/api';
import { useVerificationTimer } from '../hooks/useVerificationTimer';

const FindAccount = () => {
  const navigate = useNavigate();
  const [activeTab, setActiveTab] = useState('id'); // 'id' or 'password'
  const [email, setEmail] = useState('');
  const [memId, setMemId] = useState('');
  const [verificationCode, setVerificationCode] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [newPasswordConfirm, setNewPasswordConfirm] = useState('');
  const [isVerified, setIsVerified] = useState(false);
  const [foundId, setFoundId] = useState('');
  const { timer, isSendingCode, setIsSendingCode, startTimer, resetTimer, formatTimer, isTimerActive } = useVerificationTimer();

  //        
  const handleTabChange = (newTab) => {
    setActiveTab(newTab);
    setEmail('');
    setMemId('');
    setVerificationCode('');
    setNewPassword('');
    setNewPasswordConfirm('');
    resetTimer();
    setIsVerified(false);
    setFoundId('');
  };

  //   
  const handleBackToLogin = () => {
    navigate('/login');
  };

  //    
  const isSocialAccount = (memId) => {
    if (!memId) return false;
    return memId.startsWith('KAKAO_') || memId.startsWith('NAVER_') || memId.startsWith('GOOGLE_');
  };

  // /    
  const handleSendVerificationCode = async (e) => {
    e.preventDefault();

    if (activeTab === 'id') {
      if (!email.trim()) {
        alert(' .');
        return;
      }

      if (!validateEmail(email)) {
        alert('   .');
        return;
      }

      setIsSendingCode(true);
      try {
        await sendFindIdVerificationCode(email);
        alert('  .');
        startTimer();
        setIsVerified(false);
      } catch (error) {
        console.error('  :', error);
        alert(error.message || '    .');
      } finally {
        setIsSendingCode(false);
      }
    } else {
      if (!memId.trim()) {
        alert(' .');
        return;
      }
      
      //    
      if (isSocialAccount(memId)) {
        alert('   .      .      .');
        return;
      }
      
      if (!email.trim()) {
        alert(' .');
        return;
      }

      if (!validateEmail(email)) {
        alert('   .');
        return;
      }

      setIsSendingCode(true);
      try {
        await sendResetPasswordVerificationCode(memId, email);
        alert('  .');
        startTimer();
        setIsVerified(false);
      } catch (error) {
        console.error('  :', error);
        alert(error.message || '    .');
      } finally {
        setIsSendingCode(false);
      }
    }
  };

  //     
  const handleFindId = async (e) => {
    e.preventDefault();

    if (!verificationCode.trim()) {
      alert(' .');
      return;
    }

    try {
      const data = await findId(email, verificationCode);
      
      //    
      if (isSocialAccount(data.memId)) {
        alert('   .      .');
        setFoundId('');
        setIsVerified(false);
        return;
      }
      
      setFoundId(data.memId);
      setIsVerified(true);
      resetTimer();
      alert('  .');
    } catch (error) {
      console.error('  :', error);
      alert(error.message || '    .');
    }
  };

  //   
  const handleResetPassword = async (e) => {
    e.preventDefault();

    if (!verificationCode.trim()) {
      alert(' .');
      return;
    }
    if (!newPassword.trim()) {
      alert('  .');
      return;
    }
    if (newPassword.length < 8) {
      alert(' 8  .');
      return;
    }
    if (newPassword !== newPasswordConfirm) {
      alert('  .');
      return;
    }

    try {
      await resetPassword(memId, email, verificationCode, newPassword);
      alert(' . .');
      navigate('/login');
    } catch (error) {
      console.error('  :', error);
      alert(error.message || '    .');
    }
  };

  return (
    <div className="find-account-container">
      <div className="find-account-card">
        <div className="find-account-header">
          <button className="back-button" onClick={handleBackToLogin}>
            <img src={BackIcon} alt="" />
            <span> </span>
          </button>
          <h1 className="find-account-title"> </h1>
          <p className="find-account-subtitle">   </p>
        </div>

        <div className="find-account-body">
          <div className="tab-container">
            <button
              className={`tab-button ${activeTab === 'id' ? 'active' : ''}`}
              onClick={() => handleTabChange('id')}
            >
               
            </button>
            <button
              className={`tab-button ${activeTab === 'password' ? 'active' : ''}`}
              onClick={() => handleTabChange('password')}
            >
               
            </button>
          </div>

          <div style={{ 
            marginTop: '16px', 
            padding: '12px', 
            backgroundColor: '#F3F3F5', 
            borderRadius: '8px',
            fontSize: '13px',
            color: '#717182',
            lineHeight: '1.5'
          }}>
            <strong style={{ color: '#0A0A0A' }}>:</strong>  (, , )   /     .      .
          </div>

          {activeTab === 'id' ? (
            <>
              {!foundId ? (
                <form onSubmit={handleSendVerificationCode} className="find-account-form">
                  <div className="input-group">
                    <label> *</label>
                    <input
                      type="email"
                      placeholder="example@email.com"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      className="find-account-input"
                      disabled={isTimerActive}
                    />
                  </div>

                  <button 
                    type="submit" 
                    className="verify-button"
                    disabled={isSendingCode || isTimerActive}
                  >
                    {isSendingCode ? '...' : isTimerActive ? `(${formatTimer()})` : ' '}
                  </button>
                </form>
              ) : null}

              {isTimerActive && !foundId && (
                <form onSubmit={handleFindId} className="find-account-form" style={{ marginTop: '20px' }}>
                  <div className="input-group">
                    <label> *</label>
                    <input
                      type="text"
                      placeholder=" 6 "
                      value={verificationCode}
                      onChange={(e) => setVerificationCode(e.target.value)}
                      className="find-account-input"
                      maxLength="6"
                    />
                  </div>

                  <button type="submit" className="verify-button">
                     
                  </button>
                </form>
              )}

              {foundId && (
                <div className="find-account-result">
                  <div style={{ padding: '20px', textAlign: 'center' }}>
                    <h3 style={{ marginBottom: '10px' }}>  </h3>
                    <p style={{ fontSize: '18px', fontWeight: 'bold', color: '#155DFC' }}>
                      {foundId}
                    </p>
                    <button 
                      onClick={handleBackToLogin}
                      style={{
                        marginTop: '20px',
                        padding: '10px 20px',
                        backgroundColor: '#030213',
                        color: 'white',
                        border: 'none',
                        borderRadius: '8px',
                        cursor: 'pointer'
                      }}
                    >
                       
                    </button>
                  </div>
                </div>
              )}
            </>
          ) : (
            <>
              {!isVerified ? (
                <>
                  <form onSubmit={handleSendVerificationCode} className="find-account-form">
                    <div className="input-group">
                      <label> *</label>
                      <input
                        type="text"
                        placeholder=" "
                        value={memId}
                        onChange={(e) => setMemId(e.target.value)}
                        className="find-account-input"
                        disabled={isTimerActive}
                      />
                    </div>

                    <div className="input-group">
                      <label> *</label>
                      <input
                        type="email"
                        placeholder="example@email.com"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        className="find-account-input"
                        disabled={isTimerActive}
                      />
                    </div>

                    <button 
                      type="submit" 
                      className="verify-button"
                      disabled={isSendingCode || isTimerActive}
                    >
                      {isSendingCode ? '...' : isTimerActive ? `(${formatTimer()})` : ' '}
                    </button>
                  </form>

                  {isTimerActive && (
                    <form onSubmit={handleResetPassword} className="find-account-form" style={{ marginTop: '20px' }}>
                      <div className="input-group">
                        <label> *</label>
                        <input
                          type="text"
                          placeholder=" 6 "
                          value={verificationCode}
                          onChange={(e) => setVerificationCode(e.target.value)}
                          className="find-account-input"
                          maxLength="6"
                        />
                      </div>

                      <div className="input-group">
                        <label>  *</label>
                        <input
                          type="password"
                          placeholder="8  "
                          value={newPassword}
                          onChange={(e) => setNewPassword(e.target.value)}
                          className="find-account-input"
                        />
                      </div>

                      <div className="input-group">
                        <label>   *</label>
                        <input
                          type="password"
                          placeholder="  "
                          value={newPasswordConfirm}
                          onChange={(e) => setNewPasswordConfirm(e.target.value)}
                          className="find-account-input"
                        />
                      </div>

                      <button type="submit" className="verify-button">
                         
                      </button>
                    </form>
                  )}
                </>
              ) : null}
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default FindAccount;

