import React, { useState } from 'react';
import '../css/EventPage.css';

import welcomeImg from '../images/event/event_welcome.png'; //  
import holidayImg from '../images/event/event_holiday.png'; //  
import reviewImg from '../images/event/event_review.png'; //  
import luckyboxImg from '../images/event/event_luckybox.png'; //  

//  
const EVENT_DATA = [
  {
    id: 1,
    title: '  2,000P  !',
    period: '2025.11.01 ~   ',
    status: 'ing', // ing: , end: 
    image: welcomeImg,
    color: '#FFD700'
  },
  {
    id: 2,
    title: ' , 12   & ',
    period: '2025.12.01 ~ 2025.12.19',
    status: 'ing',
    image: holidayImg,
    color: '#FF6B6B'
  },
  {
    id: 3,
    title: '    ',
    period: ' 1 ~ ',
    status: 'ing',
    image: reviewImg,
    color: '#4ECDC4'
  },
  {
    id: 4,
    title: '[]    ',
    period: '2025.11.01 ~ 2025.11.31',
    status: 'end',
    image: luckyboxImg,
    color: '#ccc'
  }
];

function EventPage() {
  const [filter, setFilter] = useState('all'); // all, ing, end

  //  
  const filteredEvents = EVENT_DATA.filter(event => {
    if (filter === 'all') return true;
    return event.status === filter;
  });

  return (
    <div className="event-container">
      <div className="event-header">
        <h1>EVENT</h1>
        <p>Coco   .</p>
      </div>

      {/*   */}
      <div className="event-tabs">
        <button className={filter === 'all' ? 'active' : ''} onClick={() => setFilter('all')}></button>
        <button className={filter === 'ing' ? 'active' : ''} onClick={() => setFilter('ing')}> </button>
        <button className={filter === 'end' ? 'active' : ''} onClick={() => setFilter('end')}> </button>
      </div>

      {/*   () */}
      <div className="event-list">
        {filteredEvents.map((event) => (
          <div key={event.id} className="event-card">
            {/*   ,    */}
            <div 
              className="event-thumbnail" 
              style={{ 
                backgroundImage: event.image ? `url(${event.image})` : 'none',
                backgroundColor: event.image ? 'transparent' : event.color
              }}
            >
              <span className={`status-badge ${event.status}`}>
                {event.status === 'ing' ? '' : ''}
              </span>
            </div>
            
            <div className="event-info">
              <h3 className="event-title">{event.title}</h3>
              <p className="event-period">{event.period}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default EventPage;