import { useState, useMemo, useEffect } from "react";

function useTag(tags) {
    const ptagsList = useMemo(
        () => (tags ? tags.filter(tag => tag.tagStatus == "") : []), [tags]
    )

    const ntagsList = useMemo(
        () => (tags ? tags.filter(tag => tag.tagStatus == "") : []), [tags]
    )

    const [ptagsClicked, setPtagsClicked] = useState(Array(ptagsList.length).fill(false));
    const [ntagsClicked, setNtagsClicked] = useState(Array(ntagsList.length).fill(false));
    const [pWarnMsg, pSetWarnMsg] = useState("");
    const [nWarnMsg, nSetWarnMsg] = useState("");

    // const [ptagArr, setPtagArr] = useState([]);
    // const [ntagArr, setNtagArr] = useState([]);

    useEffect(() => {
        setPtagsClicked(Array(ptagsList.length).fill(false));
        setNtagsClicked(Array(ntagsList.length).fill(false));
    }, [ptagsList, ntagsList])

    const ptoggleActive = (indexTogle) => {

        setPtagsClicked((prevStates) => {

            const currentState = prevStates.filter(Boolean).length;
            const isAdding = !prevStates[indexTogle];

            if (isAdding && currentState >= 4) {
                pSetWarnMsg(" 4    .");
                return prevStates;
            }

            pSetWarnMsg("");
            return prevStates.map((currentState, index) => {
                return indexTogle == index ? !currentState : currentState;
            })
        })
    };

    const ntoggleActive = (indexTogle) => {
        setNtagsClicked((prevStates) => {
            const currentState = prevStates.filter(Boolean).length; // true 
            const isAdding = !prevStates[indexTogle]// isAdding true  , false  

            if (isAdding && currentState >= 4) {
                nSetWarnMsg(" 4    .");
                return prevStates;
            }
            nSetWarnMsg("");
            return prevStates.map((currentState, index) => {
                return indexTogle == index ? !currentState : currentState;
            })
        })
    };

    return {
        ptagsList,
        ntagsList,
        ptagsClicked,
        ntagsClicked,
        pWarnMsg,
        nWarnMsg,
        ptoggleActive,
        ntoggleActive,
        setPtagsClicked,
        setNtagsClicked
    }
}

export default useTag;