import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../css/SignupInfo.css';
import { 
  checkIdDuplicate, 
  checkNicknameDuplicate, 
  sendEmailVerificationCode, 
  verifyEmailCode,
  signup,
  validateEmail
} from '../utils/api';
import { useVerificationTimer } from '../hooks/useVerificationTimer';

const SignupInfo = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    userId: '',
    password: '',
    passwordConfirm: '',
    nickname: '',
    name: '',
    phoneNumber: '',
    email: '',
    zipcode: '',
    address1: '',
    address2: '',
    verificationCode: ''
  });
  const [isIdChecked, setIsIdChecked] = useState(false);
  const [isNicknameChecked, setIsNicknameChecked] = useState(false);
  const [isPhoneVerified, setIsPhoneVerified] = useState(false);
  const { timer, isSendingCode, setIsSendingCode, startTimer, resetTimer, formatTimer, isTimerActive } = useVerificationTimer();

  //        
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    if (name === 'userId') {
      setIsIdChecked(false);
    }
    if (name === 'nickname') {
      setIsNicknameChecked(false);
    }
  };

  //      
  const handleCheckDuplicate = async (type) => {
    try {
      if (type === 'id') {
        if (formData.userId.length < 4) {
          alert(' 4  .');
          return;
        }
        const data = await checkIdDuplicate(formData.userId);
        
        if (data.available) {
          setIsIdChecked(true);
          alert('  .');
        } else {
          alert(data.message || '   .');
          setIsIdChecked(false);
        }
      } else if (type === 'nickname') {
        if (formData.nickname.length < 2) {
          alert(' 2  .');
          return;
        }
        const data = await checkNicknameDuplicate(formData.nickname);
        
        if (data.available) {
          setIsNicknameChecked(true);
          alert('  .');
        } else {
          alert(data.message || '   .');
          setIsNicknameChecked(false);
        }
      }
    } catch (error) {
      console.error(' :', error);
      alert(error.message || '   .  .');
    }
  };

  //    
  const handleSendVerificationCode = async () => {
    if (!formData.email) {
      alert(' .');
      return;
    }

    if (!validateEmail(formData.email)) {
      alert('   .');
      return;
    }

    setIsSendingCode(true);
    try {
      await sendEmailVerificationCode(formData.email);
      alert('  .');
      startTimer();
      setIsPhoneVerified(false);
    } catch (error) {
      console.error('  :', error);
      alert(error.message || '    .');
    } finally {
      setIsSendingCode(false);
    }
  };

  //    
  const handleVerifyCode = async () => {
    if (!formData.verificationCode) {
      alert(' .');
      return;
    }

    if (!formData.email) {
      alert(' .');
      return;
    }

    try {
      await verifyEmailCode(formData.email, formData.verificationCode);
      setIsPhoneVerified(true);
      resetTimer();
      alert(' .');
    } catch (error) {
      console.error('  :', error);
      alert(error.message || '  .');
      setIsPhoneVerified(false);
    }
  };

  //   API    
  const handleAddressSearch = () => {
    if (window.daum && window.daum.Postcode) {
      new window.daum.Postcode({
        oncomplete: function(data) {
          let addr = '';
          
          if (data.userSelectedType === 'R') {
            addr = data.roadAddress;
          } else {
            addr = data.jibunAddress;
          }

          setFormData(prev => ({
            ...prev,
            zipcode: data.zonecode,
            address1: addr
          }));
          
          document.getElementById('address2').focus();
        }
      }).open();
    } else {
      alert('     .  .');
    }
  };

  //       
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!isIdChecked) {
      alert('  .');
      return;
    }
    if (formData.password.length < 8) {
      alert(' 8  .');
      return;
    }
    if (formData.password !== formData.passwordConfirm) {
      alert('  .');
      return;
    }
    if (!isNicknameChecked) {
      alert('  .');
      return;
    }
    if (!formData.name || formData.name.trim() === '') {
      alert(' .');
      return;
    }
    if (!formData.email || formData.email.trim() === '') {
      alert(' .');
      return;
    }
    if (!isPhoneVerified) {
      alert('  .');
      return;
    }

    try {
      const signupData = {
        memId: formData.userId,
        memPwd: formData.password,
        memNickname: formData.nickname,
        memName: formData.name,
        memMail: formData.email || '',
        memHp: formData.phoneNumber,
        memZipcode: formData.zipcode || '',
        memAddress1: formData.address1 || '',
        memAddress2: formData.address2 || ''
      };

      await signup(signupData);
      alert(' .');
      navigate('/login');
    } catch (error) {
      console.error(' :', error);
      alert(error.message || '   .  .');
    }
  };

  //    
  const handleBack = () => {
    navigate('/signup/terms');
  };

  return (
    <div className="signup-info-container">
      <div className="signup-info-card">
        <div className="signup-info-header">
          <h1 className="signup-info-title"></h1>
          <p className="signup-info-subtitle"> </p>
        </div>

        <form onSubmit={handleSubmit} className="signup-info-body">
          <div className="input-group">
            <label> *</label>
            <div className="input-with-button">
              <input
                type="text"
                name="userId"
                placeholder="4  "
                value={formData.userId}
                onChange={handleInputChange}
                className="signup-input"
              />
              <button
                type="button"
                className="check-button"
                onClick={() => handleCheckDuplicate('id')}
              >
                
              </button>
            </div>
          </div>

          <div className="input-group">
            <label> *</label>
            <input
              type="password"
              name="password"
              placeholder="8  "
              value={formData.password}
              onChange={handleInputChange}
              className="signup-input"
            />
          </div>

          <div className="input-group">
            <label>  *</label>
            <input
              type="password"
              name="passwordConfirm"
              placeholder="  "
              value={formData.passwordConfirm}
              onChange={handleInputChange}
              className="signup-input"
            />
          </div>

          <div className="input-group">
            <label> *</label>
            <div className="input-with-button">
              <input
                type="text"
                name="nickname"
                placeholder="2  "
                value={formData.nickname}
                onChange={handleInputChange}
                className="signup-input"
              />
              <button
                type="button"
                className="check-button"
                onClick={() => handleCheckDuplicate('nickname')}
              >
                
              </button>
            </div>
          </div>

          <div className="input-group">
            <label> *</label>
            <input
              type="text"
              name="name"
              placeholder=" "
              value={formData.name}
              onChange={handleInputChange}
              className="signup-input"
            />
          </div>

          <div className="input-group">
            <label> *</label>
            <div className="input-with-button">
              <input
                type="email"
                name="email"
                placeholder="example@email.com"
                value={formData.email}
                onChange={handleInputChange}
                className="signup-input"
                disabled={isPhoneVerified}
              />
              <button
                type="button"
                className="check-button"
                onClick={handleSendVerificationCode}
                disabled={isSendingCode || isTimerActive || isPhoneVerified}
              >
                {isSendingCode ? '...' : isTimerActive ? `(${formatTimer()})` : ''}
              </button>
            </div>
          </div>

          {isTimerActive && !isPhoneVerified && (
            <div className="input-group">
              <label> *</label>
              <div className="input-with-button">
                <input
                  type="text"
                  name="verificationCode"
                  placeholder=" 6 "
                  value={formData.verificationCode}
                  onChange={handleInputChange}
                  className="signup-input"
                  maxLength="6"
                />
                <button
                  type="button"
                  className="check-button"
                  onClick={handleVerifyCode}
                >
                  
                </button>
              </div>
              {isPhoneVerified && (
                <span style={{ color: 'green', fontSize: '12px', marginTop: '4px' }}>
                    
                </span>
              )}
            </div>
          )}

          {isPhoneVerified && (
            <div className="input-group">
              <span style={{ color: 'green', fontSize: '14px', fontWeight: '500' }}>
                   .
              </span>
            </div>
          )}

          <div className="input-group">
            <label></label>
            <input
              type="tel"
              name="phoneNumber"
              placeholder="010-1234-5678"
              value={formData.phoneNumber}
              onChange={handleInputChange}
              className="signup-input"
            />
          </div>

          <div className="input-group">
            <label></label>
            <div className="input-with-button">
              <input
                type="text"
                name="zipcode"
                placeholder=""
                value={formData.zipcode}
                onChange={handleInputChange}
                className="signup-input"
                readOnly
              />
              <button
                type="button"
                className="check-button"
                onClick={handleAddressSearch}
              >
                
              </button>
            </div>
          </div>

          <div className="input-group">
            <label></label>
            <input
              type="text"
              name="address1"
              placeholder=""
              value={formData.address1}
              onChange={handleInputChange}
              className="signup-input"
              readOnly
            />
          </div>

          <div className="input-group">
            <label></label>
            <input
              type="text"
              id="address2"
              name="address2"
              placeholder=" "
              value={formData.address2}
              onChange={handleInputChange}
              className="signup-input"
            />
          </div>

          <div className="button-group">
            <button type="button" className="cancel-button" onClick={handleBack}>
              
            </button>
            <button type="submit" className="submit-button">
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default SignupInfo;

