import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';
import Spinner from '../../components/admin/Spinner';
import CategoryTable from '../../components/admin/CategoryTable';
import '../../css/admin/AdminCategoryList.css'; // CSS  

/**
 * [AdminCategoryList]    
 * :
 * 1.    (/   )
 * 2.    (    )
 * 3.     
 */

function AdminCategoryList() {
  const [categories, setCategories] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  //    (    )
  const [newCategoryName, setNewCategoryName] = useState('');
  const [parentCategoryNo, setParentCategoryNo] = useState('');
  const [editId, setEditId] = useState(null); //    ID (null  )

  //  
  const loadCategories = async () => {
    try {
      const response = await axios.get('http://13.231.28.89:18080/api/categories');
      setCategories(response.data);
    } catch (error) {
      console.error(error);
      toast.error('  ');
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    loadCategories();
  }, []);

  //     ,      
  useEffect(() => {
    if (categories.length > 0 && !editId) {
      //  
      const parents = categories.filter(c => !c.parentCategory && !c.parentCategoryNo);
      if (parents.length > 0) {
        setParentCategoryNo(parents[0].categoryNo);
      }
    }
  }, [categories, editId]);

  //  
  const resetForm = () => {
    setNewCategoryName('');
    setEditId(null);
    //      
    const parents = categories.filter(c => !c.parentCategory && !c.parentCategoryNo);
    if (parents.length > 0) {
      setParentCategoryNo(parents[0].categoryNo);
    }
  };

  /**
   *     (/  )
   * - editId    POST()  PUT()  
   */
  const handleSubmit = async (e) => {
    e.preventDefault();

    //  
    if (!newCategoryName.trim()) {
      toast.warn('  .');
      return;
    }

    //    
    if (!editId && !parentCategoryNo) {
      toast.warn('   .');
      return;
    }

    // / :  ID  ,   
    const categoryData = {
      categoryName: newCategoryName,
      parentCategoryNo: parentCategoryNo || null
    };

    try {
      //  
      const token = localStorage.getItem('token');
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };

      if (editId) {
        //  (PUT)
        await axios.put(`http://13.231.28.89:18080/api/admin/categories/${editId}`, categoryData, config);
        toast.success('.');
      } else {
        //  (POST)
        await axios.post('http://13.231.28.89:18080/api/admin/categories', categoryData, config);
        toast.success('.');
      }

      resetForm(); //  
      loadCategories(); //   (  )
    } catch (error) {
      console.error(error);
      toast.error(' .');
    }
  };

  //   
  const handleEditClick = (category) => {
    setEditId(category.categoryNo);
    setNewCategoryName(category.categoryName);

    //    ID,   
    if (category.parentCategoryNo) {
      setParentCategoryNo(category.parentCategoryNo);
    } else if (category.parentCategory) {
      setParentCategoryNo(category.parentCategory.categoryNo);
    } else {
      setParentCategoryNo('');
    }

    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  //   
  const handleDeleteCategory = async (category) => {
    if (window.confirm(`'${category.categoryName}'  ?`)) {
      try {
        //  
        const token = localStorage.getItem('token');

        await axios.delete(`http://13.231.28.89:18080/api/admin/categories/${category.categoryNo}`, {
          headers: { Authorization: `Bearer ${token}` }
        });

        toast.success('.');
        loadCategories();
      } catch (error) {
        console.error(error);
        toast.error('  (    )');
      }
    }
  };

  if (isLoading) return <Spinner />;

  return (
    <div className="admin-page-container">
      <h2 className="page-title"> </h2>

      {/*  /   */}
      <div className="admin-card form-card">
        <h3 className="content-title">
          {editId ? ' ' : '  '}
        </h3>

        <form className="category-form" onSubmit={handleSubmit}>
          <div className="form-group">
            <label> </label>
            <select
              className="admin-input"
              value={parentCategoryNo}
              onChange={(e) => setParentCategoryNo(e.target.value)}
            >
              {/*    */}
              {categories
                .filter(cat => !cat.parentCategory && !cat.parentCategoryNo)
                .map((cat) => (
                  <option key={cat.categoryNo} value={cat.categoryNo}>
                    {cat.categoryName}
                  </option>
                ))}
            </select>
          </div>

          <div className="form-group">
            <label> </label>
            <input
              className="admin-input"
              type="text"
              value={newCategoryName}
              onChange={(e) => setNewCategoryName(e.target.value)}
              placeholder=": ,  "
            />
          </div>

          <div className="button-group">
            <button type="submit" className="btn btn-primary">
              {editId ? ' ' : ''}
            </button>
            {editId && (
              <button type="button" className="btn btn-secondary" onClick={resetForm}>
                
              </button>
            )}
          </div>
        </form>
      </div>

      {/*     */}
      <div className="admin-card">
        <div className="card-header">
          <h3 className="content-title"> </h3>
        </div>
        {/*    */}
        <CategoryTable
          categories={categories}
          onEdit={handleEditClick}
          onDelete={handleDeleteCategory}
        />
      </div>
    </div>
  );
}

export default AdminCategoryList;