import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { toast } from 'react-toastify';
import Pagination from '../../components/admin/Pagination';
import Spinner from '../../components/admin/Spinner';
import '../../css/admin/AdminProductList.css';
import editIcon from '../../images/edit.svg';
import deleteIcon from '../../images/delete.svg';

/**
 * [AdminProductList]    
 * :
 * 1.     (, , / )
 * 2.     (, , ,  )
 * 3.  ( )      
 */

const LIMIT = 6;

function AdminProductList() {
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  //   
  const [searchTerm, setSearchTerm] = useState(''); //  
  const [debouncedSearchTerm, setDebouncedSearchTerm] = useState(''); //    (API)
  const [debounceTimer, setDebounceTimer] = useState(null);
  const [selectedCategory, setSelectedCategory] = useState('');
  const [selectedStatus, setSelectedStatus] = useState('ALL');
  const [sortOrder, setSortOrder] = useState('idAsc'); //  : ID 

  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);

  //  
  const [dashboardCounts, setDashboardCounts] = useState({
    totalProducts: 0,
    inStock: 0,
    outOfStock: 0,
    totalStock: 0
  });

  //  
  useEffect(() => {
    axios.get('http://13.231.28.89:18080/api/categories')
      .then(res => setCategories(res.data))
      .catch(err => console.error("  :", err));
  }, []);

  //      
  useEffect(() => {
    const loadData = async () => {
      setIsLoading(true);
      try {
        //   
        const params = {
          page: currentPage,
          size: LIMIT,
          sort: sortOrder,
          q: debouncedSearchTerm || undefined, //   
          categoryNo: selectedCategory || undefined,
          status: selectedStatus || undefined
        };

        //  
        const token = localStorage.getItem('token');

        //   
        const productRes = await axios.get('http://13.231.28.89:18080/api/products', { params });
        setProducts(productRes.data.content);
        setTotalPages(productRes.data.totalPages);

        //  
        const statsRes = await axios.get('http://13.231.28.89:18080/api/admin/stats', {
          headers: { Authorization: `Bearer ${token}` }
        });

        setDashboardCounts({
          totalProducts: statsRes.data.totalProducts,
          inStock: statsRes.data.inStockProducts,
          outOfStock: statsRes.data.outOfStockProducts,
          totalStock: statsRes.data.totalStock
        });

      } catch (error) {
        console.error("  :", error);
        toast.error("   .");
      } finally {
        setIsLoading(false);
      }
    };

    loadData();
  }, [currentPage, debouncedSearchTerm, selectedCategory, selectedStatus, sortOrder]);

  /**
   *   
   * -       (Soft Delete) 
   * -   UI      
   */
  const handleDelete = async (product) => {
    if (window.confirm(` ?\n: ${product.prdName}`)) {
      try {
        //  
        const token = localStorage.getItem('token');

        await axios.delete(`http://13.231.28.89:18080/api/admin/products/${product.prdNo}`, {
          headers: { Authorization: `Bearer ${token}` }
        });

        toast.success('.');

        //  
        if (products.length === 1 && currentPage > 1) {
          setCurrentPage(prev => prev - 1);
        } else {
          //  UI     
          setProducts(prev => prev.filter(p => p.prdNo !== product.prdNo));
          setDashboardCounts(prev => ({ ...prev, totalProducts: prev.totalProducts - 1 }));
        }
      } catch (error) {
        toast.error("   .");
      }
    }
  };

  const handleFilterChange = (setter) => (e) => {
    const value = e.target.value;

    if (setter === setSearchTerm) {
      //   UI 
      setSearchTerm(value);

      //   
      if (debounceTimer) clearTimeout(debounceTimer);

      const newTimer = setTimeout(() => {
        setDebouncedSearchTerm(value);
        setCurrentPage(1); //   1 
      }, 500);
      setDebounceTimer(newTimer);
    } else {
      //     
      setter(value);
      setCurrentPage(1);
    }
  };

  return (
    <div className="admin-page-container">
      {/*    */}
      <h2 className="page-title"> </h2>

      {/*   */}
      <div className="dashboard-container">
        <div className="dash-card">
          <p className="dash-title"> </p>
          <p className="dash-value">{dashboardCounts.totalProducts}</p>
        </div>
        <div className="dash-card">
          <p className="dash-title"></p>
          <p className="dash-value">{dashboardCounts.inStock}</p>
        </div>
        <div className="dash-card">
          <p className="dash-title"></p>
          <p className="dash-value">{dashboardCounts.outOfStock}</p>
        </div>
        <div className="dash-card">
          <p className="dash-title"> </p>
          <p className="dash-value">{dashboardCounts.totalStock.toLocaleString()}</p>
        </div>
      </div>

      <div className="admin-content-card">
        <div className="content-header">
          <h3> </h3>
          <div className="header-actions">
            <button className="btn-refresh" onClick={() => window.location.reload()}> </button>
            <Link to="/admin/product/new" className="btn-add-product">+  </Link>
          </div>
        </div>

        {/*   */}
        <div className="filter-container">
          <input
            type="text"
            className="search-input"
            placeholder=" ..."
            value={searchTerm}
            onChange={handleFilterChange(setSearchTerm)}
          />

          <select className="filter-select" value={selectedCategory} onChange={handleFilterChange(setSelectedCategory)}>
            <option value=""> </option>
            {categories.filter(c => !c.parentCategoryNo).map(cat => (
              <React.Fragment key={cat.categoryNo}>
                <option value={cat.categoryNo}>{cat.categoryName}</option>
                {categories.filter(sub => sub.parentCategoryNo === cat.categoryNo).map(sub => (
                  <option key={sub.categoryNo} value={sub.categoryNo}>&nbsp;&nbsp; {sub.categoryName}</option>
                ))}
              </React.Fragment>
            ))}
          </select>

          <select className="filter-select" value={selectedStatus} onChange={handleFilterChange(setSelectedStatus)}>
            <option value="ALL"> </option>
            <option value="SALE"></option>
            <option value="SOLD_OUT"></option>
            <option value="STOP"></option>
          </select>

          <select className="filter-select" value={sortOrder} onChange={(e) => { setSortOrder(e.target.value); setCurrentPage(1); }}>
            <option value="idAsc"> (ID)</option>
            <option value="newest"></option>
            <option value="popularity"></option>
            <option value="priceAsc"> </option>
            <option value="priceDesc"> </option>
          </select>
        </div>

        {/*   */}
        <div className="table-wrapper">
          <table className="admin-table">
            <thead>
              <tr>
                <th style={{ width: '60px' }}>ID</th>
                <th style={{ width: '80px' }}></th>
                <th></th>
                <th style={{ width: '120px' }}></th>
                <th style={{ width: '100px' }}></th>
                <th style={{ width: '80px' }}></th>
                <th style={{ width: '80px' }}></th>
                <th style={{ width: '120px' }}></th>
              </tr>
            </thead>
            <tbody>
              {isLoading ? (
                <tr><td colSpan="8" className="loading-cell"><Spinner /></td></tr>
              ) : products.length > 0 ? (
                products.map((product) => (
                  <tr key={product.prdNo}>
                    <td>{product.prdNo}</td>
                    <td>
                      <img
                        src={product.imageUrl || '/prd_placeholder.png'}
                        alt=""
                        className="product-thumb"
                        onError={(e) => {
                          e.target.onerror = null; //   
                          e.target.src = '/prd_placeholder.png';
                        }}
                      />
                    </td>
                    <td className="fw-bold">{product.prdName}</td>
                    <td>{product.categoryName}</td>
                    <td>{product.prdPrice.toLocaleString()}</td>
                    <td>{product.stock}</td>
                    <td>
                      {/*  CSS    */}
                      <span className={`status-tag ${product.status === '' ? 'status-sale' :
                        product.status === '' ? 'status-soldout' : 'status-stop'
                        }`}>
                        {product.status}
                      </span>
                    </td>
                    {/* ,    */}
                    <td>
                      <Link to={`/admin/product/edit/${product.prdNo}`} className="icon-btn edit" title="">
                        <img src={editIcon} alt="" />
                      </Link>
                      <button onClick={() => handleDelete(product)} className="icon-btn delete" title="">
                        <img src={deleteIcon} alt="" />
                      </button>
                    </td>
                  </tr>
                ))
              ) : (
                <tr><td colSpan="8" className="empty-cell">  .</td></tr>
              )}
            </tbody>
          </table>
        </div>

        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={(page) => setCurrentPage(page)}
        />
      </div>
    </div>
  );
}

export default AdminProductList;