import { useRef, useState } from "react";
import { v4 as uuidv4 } from "uuid"; 
function useFile() {
    const ref = useRef(null);
    const [previewFiles, setPreviewFiles] = useState([]); // {id, url}  
    const [fileError, setFileError] = useState("");

    const processFile = (file) => {

        return new Promise((resolve, reject) => {

            const reader = new FileReader();

            reader.onload = (event) => {
                resolve({
                    id: uuidv4(), //  ID
                    url: event.target.result, //  URL
                    file: file
                });
            };
            //      id, url, file .

            //   error  .
            reader.onerror = (error) => reject(error);

            //    
            reader.readAsDataURL(file);


        });
    };

    const handleFileChange = async (e) => {
        const newFiles = Array.from(e.target.files);
        if (newFiles.length === 0) return;

        const currentCount = previewFiles.length;
        const remainingSlots = 5 - currentCount;

        if (newFiles.length > remainingSlots) {
            setFileError(` 5   . ${remainingSlots} .`);
            newFiles.splice(remainingSlots);
        } else {
            setFileError("");
        }
        const newFileObjects = await Promise.all(newFiles.map(processFile));
        setPreviewFiles(prevFiles => [...prevFiles, ...newFileObjects]);

        e.target.value = null;
    };

    const handleDelete = (idToDelete) => {
        setPreviewFiles(prevFiles =>
            prevFiles.filter(file => file.id !== idToDelete)
        );
        if (previewFiles.length - 1 < 5) {
            setFileError("");
        }
    };

    const handleAddImageClick = () => {
        if (previewFiles.length < 5) {
            setFileError("");
            ref.current.click();
        } else {
            setFileError(" 5   .");
        }
    };

    return{
        previewFiles,
        setPreviewFiles,
        handleDelete,
        handleAddImageClick,
        handleFileChange,
        ref,
        fileError
    }

}

export default useFile;