import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import "../css/OrderDetail.css";
import axios from "axios";

function OrderDetail() {
  const navigate = useNavigate();
  const { orderNo } = useParams();

  const [order, setOrder] = useState(null);
  const [loading, setLoading] = useState(true);
  
  const token = localStorage.getItem("token");
  useEffect(() => {

    if (!orderNo || !token) return;
    axios
      .get(`http://13.231.28.89:18080/api/orders/${orderNo}`, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })
      .then((res) => {
        setOrder(res.data);
      })
      .catch((err) => {
        console.error("   :", err);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [orderNo, token]);

  if (loading) return <p className="loading-text"> ...</p>;
  if (!order) return <p className="empty-order">    .</p>;
  const originalSubtotal = order.items
    .reduce((total, item) => total + (item.originalPrice || item.price || 0) * (item.qty || 1), 0);

  //      
  const shippingFee = order.shippingFee || 0;
  const pointsUsed = order.pointsUsed || 0;

  //    ( :  +  - )
  const finalPaymentAmount = originalSubtotal + shippingFee - pointsUsed;

  //   
  const handleCancelOrder = async () => {
    if (window.confirm("  ?")) {
      try {
        await axios.post(
          `http://13.231.28.89:18080/api/orders/${orderNo}/cancel`, //  API 
          null,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        alert(" .");
        //        
        window.location.reload();
      } catch (error) {
        const errorMessage = error.response?.data || "     .";
        alert(errorMessage);
      }
    }
  };


  return (
    <div className="order-detail-container">

      {/*  */}
      <button className="back-btn" onClick={() => navigate("/mypage")}>
        <svg
          className="arrow-icon"
          width="14"
          height="14"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
        >
          <line x1="19" y1="12" x2="5" y2="12"></line>
          <polyline points="12 19 5 12 12 5"></polyline>
        </svg>
        <span> </span>
      </button>

      {/*    */}
    <div className="order-header">

      <div className="order-top-row">
        <h2>{order.orderDate?.slice(0, 10).replace(/-/g, ".")}</h2>
        <span className="order-status">{order.status}</span>
      </div>

      <p className="order-no"> {order.orderNo}</p>
    </div>

      {/*   */}
      <div className="section-box">
        <h3></h3>
        <p><strong>{order.recipientName}</strong></p>
        <p>{order.recipientPhone}</p>
        <p>{order.orderAddress1} {order.orderAddress2}</p>
        {order.deliveryMessage && (
          <p className="delivery-msg">{order.deliveryMessage}</p>
        )}
      </div>

      {/*   */}
      <div className="section-box">
        <h3>  {order.items.length}</h3>

       {order.items.map((item, idx) => (
           <div key={idx} className="product-card">
          <img src={item.imageUrl} alt="" className="product-img"/>
          <div className="product-info">
            <p className="product-name">{item.productName}</p>
            <p className="product-detail">
              {item.price?.toLocaleString() || 0} / {item.qty}
            </p>
            </div>
            {order.status === "" && (
              <button
                className="review-btn"
                onClick={() => navigate(`/write-review/${item.orderItemNo}`)}
              >
                 
              </button>
            )}
          </div>
        ))}

        {order.status === "" && (
          <div className="status-actions">
            <button className="action-btn"> </button>
            <button className="action-btn"> </button>
            <button className="action-btn"> </button>
          </div>
        )}
      </div>

      {/*   */}
      <div className="section-box">
        <h3> </h3>

        <div className="price-row">
          <span> </span>
          <span>{Number(originalSubtotal).toLocaleString()}</span>
        </div>

        <div className="price-row">
          <span></span>
          <span>{Number(shippingFee).toLocaleString()}</span>
        </div>


        <div className="price-row">
          <span> </span>
          <span>-{Number(pointsUsed).toLocaleString()}</span>
        </div>
        <div className="price-total">
          <strong> </strong>
          <strong>
            {Number(finalPaymentAmount).toLocaleString()}
          </strong>
        </div>
      </div>

      <button className="list-btn" onClick={() => navigate("/order-history")}>
        
      </button>

      {/*     (PAID  PENDING   )*/}
      {(order.status === "PAID" || order.status === "PENDING") && (
        <button
          className="list-btn cancel-btn" // 'cancel-btn'   
          onClick={handleCancelOrder}
        >
           
        </button>
      )}

    </div>
  );
}

export default OrderDetail;