import React from 'react';

/**
 * [  ]
 * - :         
 */
const ProductOptionSelector = ({ options, selectedOption, onSelect }) => {
  if (!options || options.length === 0) return null;

  const selectedOpt = options.find(opt => opt.optionNo === Number(selectedOption));
  //  5  0   true
  const isLowStock = selectedOpt && selectedOpt.stock > 0 && selectedOpt.stock < 5;

  return (
    <div className="option-selector-container">
      <label htmlFor="product-option" className="visually-hidden">  </label>
      <select
        id="product-option"
        className="product-select"
        value={selectedOption}
        onChange={(e) => onSelect(e.target.value)}
      >
        <option value=""> </option>
        {options.map((opt) => (
          <option key={opt.optionNo} value={opt.optionNo} disabled={opt.stock <= 0}>
            {opt.optionValue}
            {opt.addPrice > 0 ? ` (+${opt.addPrice.toLocaleString()})` : ''}
            {opt.stock > 0 ? ` (: ${opt.stock})` : ' ()'}
          </option>
        ))}
      </select>

      {/*     */}
      {isLowStock && (
        <div className="low-stock-alert" style={{ 
          color: '#e74c3c', 
          fontSize: '16px', 
          fontWeight: '600', 
          marginTop: '10px',
          display: 'flex',
          alignItems: 'center',
          gap: '4px'
        }}>
          <svg width="14" height="14" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <path d="M10.29 3.86L1.82 18a2 2 0 0 0 1.71 3h16.94a2 2 0 0 0 1.71-3L13.71 3.86a2 2 0 0 0-3.42 0z"></path>
            <line x1="12" y1="9" x2="12" y2="13"></line>
            <line x1="12" y1="17" x2="12.01" y2="17"></line>
          </svg>
           !  {selectedOpt.stock} . !
        </div>
      )}
    </div>
  );
};

export default ProductOptionSelector;