import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import ProductDetailSkeleton from '../../components/product/detail/ProductDetailSkeleton';
import ProductImageGallery from '../../components/product/detail/ProductImageGallery';
import ProductInfoBox from '../../components/product/detail/ProductInfoBox';
import ProductTabs from '../../components/product/detail/ProductTabs';
import RecentProductSidebar from '../../components/product/detail/RecentProductSidebar';
import { getStoredMember, isLoggedIn, STORAGE_KEYS } from '../../utils/api';
import { useOrder } from '../OrderContext';
import '../../css/product/ProductDetailPage.css';

/**
 * [ProductDetailPage]    
 * :
 * 1.   (, ,  )   
 * 2.  (, , , )   
 * 3.     (, ) 
 * 4.     
 */

function ProductDetailPage() {
  const { productId } = useParams();
  const navigate = useNavigate();
  const { setOrderSubtotal } = useOrder(); //     

  //  
  const [product, setProduct] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedOption, setSelectedOption] = useState('');
  const [quantity, setQuantity] = useState(1);
  const [toastMessage, setToastMessage] = useState('');

  //     (   1 )
  useEffect(() => {
    const fetchProductDetail = async () => {
      setIsLoading(true);
      try {
        const response = await axios.get(`http://13.231.28.89:18080/api/products/${productId}`);
        const data = response.data;

        //    
        if (!data) {
            navigate('/not-found', { replace: true });
            return;
        }

        //    
        if (data.status === 'STOP' || data.status === '') {
            navigate('/product-stopped', { replace: true }); //   
            return;
        }

        setProduct(data);
      } catch (error) {
        console.error(error);
        //    (404  ) -> NotFound  
        navigate('/not-found', { replace: true });
        setProduct(null);
      } finally {
        setIsLoading(false);
      }
    };
    fetchProductDetail();
  }, [productId, navigate]);

  //   
  useEffect(() => {
    if (toastMessage) {
      const timer = setTimeout(() => setToastMessage(''), 2000);
      return () => clearTimeout(timer);
    }
  }, [toastMessage]);

  //    
  useEffect(() => {
    if (product) {
      //      " | " 
      document.title = `${product.prdName} | COCO`;
    }

    // cleanup:      
    return () => {
      document.title = 'COCO'; 
    };
  }, [product]);

  const handleBack = () => {
    navigate(-1);
  };

  /**
   *   
   * -   
   * -     
   * -   API    (/confirm) 
   */
  const handleAddToCart = async () => {
    if (!isLoggedIn()) {
      alert('  .');
      navigate('/login');
      return;
    }

    const member = getStoredMember();
    if (!member || !member.memNo) {
      alert('    .');
      return;
    }

    let optionNoToUse;
    const hasOptions = product.options && product.options.length > 0;

    if (selectedOption) {
      optionNoToUse = Number(selectedOption);
    } else if (hasOptions) {
      alert(' .');
      return;
    } else {
      alert('   .  .');
      return;
    }

    try {
      const token = localStorage.getItem(STORAGE_KEYS.TOKEN);
      await axios.post(
        'http://13.231.28.89:18080/api/coco/members/cart/items',
        {
          memNo: member.memNo,
          optionNo: optionNoToUse,
          cartQty: quantity
        },
        {
          headers: { Authorization: `Bearer ${token}` }
        }
      );

      setToastMessage('  .');
      if (window.confirm(' ?')) { navigate('/cart'); }

    } catch (error) {
      console.error(error);
      const message = error.response?.data?.message || '  .';
      alert(message);
    }
  };

  /**
   *   
   * -       
   * -      (/order)  
   * -      
   */
  const handleBuyNow = () => {
    //   
    if (!selectedOption && product.options && product.options.length > 0) {
      alert(' .');
      return;
    }

    //  
    if (!isLoggedIn()) {
      alert('  .');
      navigate('/login');
      return;
    }

    //       
    const selectedOpt = product.options?.find(opt => opt.optionNo === Number(selectedOption));
    const unitPrice = product.prdPrice + (selectedOpt?.addPrice || 0);
    const total = unitPrice * quantity;
    
    setOrderSubtotal(total); //     

    //    (  )
    // state      
    navigate('/order', { 
      state: { 
        mode: 'direct',
        orderItems: [{
          prdNo: product.prdNo,
          productName: product.prdName,
          optionNo: Number(selectedOption),
          optionName: selectedOpt?.optionName,
          cartQty: quantity,
          price: unitPrice,
          productImage: product.imageUrls?.[0]
        }],
        orderSubtotal: total
      } 
    });
  };

  if (isLoading) {
    return (
      <div className="detail-container">
        <div className="back-btn-container">
          <button className="back-btn" onClick={handleBack}>&lt; </button>
        </div>
        <ProductDetailSkeleton />
      </div>
    );
  }

  if (!product) {
    return null;
  }

  return (
    <div className="detail-page-wrapper">
      <div className="detail-container">
        {/*   */}
        <div className={`toast ${toastMessage ? 'show' : 'hide'}`}>
          {toastMessage}
        </div>

        <div className="back-btn-container">
          <button className="back-btn" onClick={handleBack}>&lt; </button>
        </div>

        <div className="top-section">
          {/*    */}
          <ProductImageGallery
            productName={product.prdName}
            imageUrls={product.imageUrls}
          />

          {/*       ( ) */}
          <ProductInfoBox
            product={product}
            selectedOption={selectedOption}
            setSelectedOption={setSelectedOption}
            quantity={quantity}
            setQuantity={setQuantity}
            handleAddToCart={handleAddToCart}
            handleBuyNow={handleBuyNow}
          />
        </div>

        {/* //   */}
        <ProductTabs product={product} />
        </div>

      {/*    ( ) */}
      <RecentProductSidebar currentProduct={product} />
    </div>
  );
}

export default ProductDetailPage;