import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';
import { getAuthHeaders } from '../../utils/api'; //     

const PointUpdateModal = ({ isOpen, onClose, member, onSuccess }) => {
  const [newPointInput, setNewPointInput] = useState('');

  //     
  useEffect(() => {
    if (isOpen && member) {
      setNewPointInput(member.point || 0);
    }
  }, [isOpen, member]);

  if (!isOpen || !member) return null;

  const savePoint = async () => {
    const newPoint = Number(newPointInput);

    if (isNaN(newPoint) || newPoint < 0) {
      alert(" (0 ) .");
      return;
    }

    try {
      const response = await axios.put(
        `http://13.231.28.89:18080/api/member/admin/${member.memNo}/point`,
        { point: newPoint },
        { headers: getAuthHeaders() }
      );

      if (response.status === 200) {
        toast.success(" .");
        onSuccess(); //      
        onClose();   //  
      }
    } catch (error) {
      console.error(error);
      toast.error("  ");
    }
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div
        className="modal-content"
        onClick={(e) => e.stopPropagation()}
        style={{
          maxWidth: '380px',
          width: '90%',
          borderRadius: '12px',
          overflow: 'hidden',
          boxShadow: '0 10px 25px rgba(0,0,0,0.1)'
        }}
      >
        {/*  */}
        <div className="modal-header" style={{ borderBottom: '1px solid #eee', padding: '15px 20px', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <h3 style={{ margin: 0, fontSize: '18px', fontWeight: 'bold' }}> </h3>
          <button
            className="btn-close"
            onClick={onClose}
            style={{ fontSize: '24px', background: 'none', border: 'none', cursor: 'pointer', color: '#999', padding: 0 }}
          >
            
          </button>
        </div>

        {/*  */}
        <div className="modal-body" style={{ padding: '30px 20px', textAlign: 'center' }}>
          <p style={{ fontSize: '16px', marginBottom: '25px', color: '#555' }}>
            <strong>{member.memName}</strong>  
          </p>

          <div style={{ marginBottom: '20px', background: '#f8f9fa', padding: '12px', borderRadius: '8px' }}>
            <span style={{ color: '#666', fontSize: '14px' }}>: </span>
            <span style={{ fontWeight: 'bold', color: '#333', fontSize: '18px', marginLeft: '5px' }}>
              {member.point?.toLocaleString()} P
            </span>
          </div>

          {/*   */}
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', marginBottom: '10px' }}>
            <input
              type="number"
              value={newPointInput}
              onChange={(e) => setNewPointInput(e.target.value)}
              placeholder="0"
              style={{
                padding: '12px',
                width: '160px',
                fontSize: '18px',
                textAlign: 'right',
                border: '1px solid #ddd',
                borderRight: 'none',
                borderRadius: '6px 0 0 6px',
                outline: 'none',
                color: '#333',
                fontWeight: '600'
              }}
            />
            {/*   (P) */}
            <span style={{
              fontSize: '16px',
              fontWeight: 'bold',
              color: '#555',
              background: '#fff',
              border: '1px solid #ddd',
              borderLeft: 'none',
              padding: '12px 15px 12px 5px',
              borderRadius: '0 6px 6px 0',
              display: 'flex',
              alignItems: 'center',
              height: '100%',
              boxSizing: 'border-box'
            }}>
              P
            </span>
          </div>
          <p style={{ fontSize: '13px', color: '#888', margin: 0 }}>
            *     .
          </p>
        </div>

        {/*  ( ) */}
        <div className="modal-footer" style={{
          borderTop: '1px solid #eee',
          padding: '15px 20px',
          display: 'flex',
          justifyContent: 'flex-end',
          gap: '8px',
          background: '#fafafa'
        }}>
          <button
            onClick={onClose}
            style={{
              padding: '10px 18px',
              border: '1px solid #ddd',
              background: 'white',
              color: '#666',
              borderRadius: '6px',
              cursor: 'pointer',
              fontWeight: '600',
              fontSize: '14px',
              transition: 'all 0.2s'
            }}
            onMouseOver={(e) => e.target.style.background = '#f1f1f1'}
            onMouseOut={(e) => e.target.style.background = 'white'}
          >
            
          </button>
          <button
            onClick={savePoint}
            style={{
              backgroundColor: '#333',
              color: 'white',
              border: '1px solid #333',
              padding: '10px 24px',
              borderRadius: '6px',
              cursor: 'pointer',
              fontWeight: '600',
              fontSize: '14px',
              transition: 'all 0.2s'
            }}
            onMouseOver={(e) => {
              e.target.style.backgroundColor = '#555';
              e.target.style.borderColor = '#555';
            }}
            onMouseOut={(e) => {
              e.target.style.backgroundColor = '#333';
              e.target.style.borderColor = '#333';
            }}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default PointUpdateModal;