import React, { useState,useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import '../../css/PaymentPage.css';
import { useOrder } from '../OrderContext'; //   (Context) 
import TermsPopup from './TermsPopup'; //     
import axios from 'axios';

//   ,  ,         (  3)
function PaymentPage() {
  const navigate = useNavigate();

  // OrderContext  (lastName, phone )  
  const {
    orderSubtotal, //  
    shippingFee,   // 
    userPoints,    //   
    pointsToUse, setPointsToUse, //   
    lastName, firstName, phone, postcode, address, addressDetail, deliveryMessage, //  
    orderItems,
    setCartItems
  } = useOrder();

  useEffect(() => {
    //   0   ( Context  )
    if (orderSubtotal <= 0) {
        
        alert("   .  .");
        navigate('/cart'); //     
    }
}, [orderSubtotal, navigate]); // orderSubtotal 0   

  //   :  ,  ,    
  const [paymentMethod, setPaymentMethod] = useState('api'); // 'api'  'card'
  const [agreements, setAgreements] = useState({
    purchase: false, //   
    info: false      //   
  });
  const [isTermsPopupOpen, setIsTermsPopupOpen] = useState(false);

  //       
  const [cardNumber, setCardNumber] = useState('');
  const [cardName, setCardName] = useState('');
  const [cardExpiry, setCardExpiry] = useState('');
  const [cardCvc, setCardCvc] = useState('');

  //   / 
  const handleOpenTermsPopup = () => setIsTermsPopupOpen(true);
  const handleCloseTermsPopup = () => setIsTermsPopupOpen(false);

  //  :      
  const totalAmount = orderSubtotal + shippingFee;
  const finalAmount = totalAmount - pointsToUse;

  //     
  const handlePointsChange = (e) => {
    let value = Number(e.target.value);
    if (value < 0) value = 0;
    if (value > userPoints) value = userPoints;       //    
    if (value > totalAmount) value = totalAmount;     //    
    setPointsToUse(value);
  };

  // ' '  
  const handleUseAllPoints = () => {
    //      (      )
    const maxUsablePoints = Math.min(userPoints, totalAmount);
    setPointsToUse(maxUsablePoints);
  };

  //     
  const handleAgreementChange = (e) => {
    const { name, checked } = e.target;
    setAgreements(prev => ({ ...prev, [name]: checked }));
  };
  const generateOrderName = (items) => {
    if (!items || items.length === 0) {
        return "Coco  ";
    }
    
    const firstItemName = items[0].productName; //    
    
    if (items.length === 1) {
        return firstItemName; //  1   
    }
    
    const remainingCount = items.length - 1; //   
    return `${firstItemName}  ${remainingCount}`; 
};


  // ''        ''  
  const handlePaymentSubmit = () => {
    // 1.    
    if (!agreements.purchase || !agreements.info) {
      alert("   .");
      return;
    }

    if (paymentMethod === 'api') {
      // 2-1. 'API ' : ''   
      
      const { IMP } = window; // index.html  IMP 
      IMP.init("iamport"); //   

      //    
      const data = {
        pg: "kakaopay", // PG (: )
        pay_method: "card", //  
        merchant_uid: `coco_order_${new Date().getTime()}`, //  
        name: generateOrderName(orderItems), // 
        amount: finalAmount, //      
        buyer_name: `${lastName}${firstName}`, //  
        buyer_tel: phone,                      //  
        buyer_addr: `${address} ${addressDetail}`, //  
        buyer_postcode: postcode,               //  
      };

      //    
      IMP.request_pay(data, (rsp) => {
        if (rsp.success) {
          // ---   ---
          console.log(" :", rsp);

         
          const orderData = {
            // 1.    ( )
              orderItems: orderItems?.map(item => ({ 
                  prdNo: Number(item.prdNo),       
                  optionNo: Number(item.optionNo),    
                  orderQty: Number(item.cartQty), 
              })),
            //  
            recipientName: lastName + firstName, 
            recipientPhone: phone,
            orderZipcode: postcode,
            orderAddress1: address,
            orderAddress2: addressDetail,
            deliveryMessage: deliveryMessage || "  .",
            //
            pointsUsed: pointsToUse,
            //  /   ID 
            impUid: rsp.imp_uid
            
          };

          //  
          const token = localStorage.getItem('token');

          //  Axios  API  (  )
          axios.post('http://13.231.28.89:18080/api/orders', orderData, {
            headers: {
              'Authorization': `Bearer ${token}`, 
              'Content-Type': 'application/json'
            }
          })
            .then((response) => {
              //      
              const realOrderNo = response.data; 
              console.log("  . :", realOrderNo);

              //    
              return axios.delete('http://13.231.28.89:18080/api/coco/members/cart/items', {
                  headers: {
                      'Authorization': `Bearer ${token}`,
                  }
              })
              
              // -     
              .then(() => {
                  console.log("  . FE  .");
                  
                  // Context  
                  if (setCartItems) { 
                      setCartItems([]); 
                  }
                  window.dispatchEvent(new Event("cartUpdated"));
                  
                  alert("  !");
                  //       
                  navigate('/order-success', { state: { orderNo: realOrderNo } });
              })
              
              // -   (  )
              .catch((cartError) => {
                  //         
                  console.error("   (  ):", cartError);
                  navigate('/order-success', { state: { orderNo: realOrderNo } });
              });
            })

            .catch((error) => {
              console.error("  :", error);
              
              // (403  :   )
              if (error.response && error.response.status === 403) {
                alert("  .  .");
                // navigate('/login');
              } else {
                alert("  : " + (error.response?.data || " "));
              }
            });

        } else {
          // ---   ---
          console.log(" :", rsp.error_msg);
          let message = "    .";
          if (rsp.error_msg.includes("") || rsp.error_msg.includes("")) {
             message = "  .";
          }

          alert(message); 
          //    state  
          navigate('/order-fail', { state: { failMessage: message } });
        }
      });

    } else if (paymentMethod === 'card') {
      // 2-2. '/'   
      
      if (!cardNumber || !cardName || !cardExpiry || !cardCvc) {
        alert("   .");
        return;
      }

      console.log(" '/  '  .  .");
      navigate('/order-fail', { state: { failMessage: "       ." } });
    }
  };

  return (
    <div className="payment-page">
      
      
      <h1></h1>

      <div className="payment-content-wrapper">
        {/* --- :     --- */}
        <div className="payment-details">
          <h2>  </h2>
          <div className="payment-method-options">
            {/* API    */}
            <div 
              className={`method-option ${paymentMethod === 'api' ? 'selected' : ''}`}
              onClick={() => setPaymentMethod('api')}
            >
              <span className="radio-icon"></span> API  (/ )
            </div>
            {/* /     */}
            <div 
              className={`method-option ${paymentMethod === 'card' ? 'selected' : ''}`}
              onClick={() => setPaymentMethod('card')}
            >
              <span className="radio-icon"></span> /  
            </div>
          </div>

          {/*   'card'       */}
          {paymentMethod === 'card' && (
            <div className="card-info-section">
              <h2> </h2>
              <form className="card-form">
                <div className="form-group">
                  <label htmlFor="card-number"> </label>
                  <input 
                    type="text" 
                    id="card-number" 
                    placeholder="1234 5678 9012 3456" 
                    value={cardNumber}
                    onChange={(e) => setCardNumber(e.target.value)}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="card-name"> </label>
                  <input 
                    type="text" 
                    id="card-name" 
                    placeholder="HONG GIL DONG" 
                    value={cardName}
                    onChange={(e) => setCardName(e.target.value)}
                  />
                </div>
                {/*   CVC    */}
                <div className="form-group-half">
                  <div className="form-group">
                    <label htmlFor="card-expiry"></label>
                    <input 
                      type="text" 
                      id="card-expiry" 
                      placeholder="MM/YY" 
                      value={cardExpiry}
                      onChange={(e) => setCardExpiry(e.target.value)}
                    />
                  </div>
                  <div className="form-group">
                    <label htmlFor="card-cvc">CVC</label>
                    <input 
                      type="text" 
                      id="card-cvc" 
                      placeholder="123" 
                      value={cardCvc}
                      onChange={(e) => setCardCvc(e.target.value)}
                    />
                  </div>
                </div>
              </form>
            </div>
          )}

          {/* ---    --- */}
          <div className="points-section">
            <h2> </h2>
            <div className="points-form form-group-with-button">
              {/*     */}
              <input 
                type="number" 
                placeholder="0"
                value={pointsToUse || ''} 
                onChange={handlePointsChange}
              />
              {/*     */}
              <button type="button" className="btn-outline" onClick={handleUseAllPoints}>
                 
              </button>
            </div>
            <p className="points-info"> : {userPoints.toLocaleString()} P</p>
          </div>
          
          {/* ---    --- */}
          <div className="agreement-section">
            <div className="agreement-item">
              <input 
                type="checkbox" 
                id="agree-purchase" 
                name="purchase"
                checked={agreements.purchase}
                onChange={handleAgreementChange}
              />
              <label htmlFor="agree-purchase">      ()</label>
            </div>
            <div className="agreement-item">
              <input 
                type="checkbox" 
                id="agree-info" 
                name="info"
                checked={agreements.info}
                onChange={handleAgreementChange}
              />
              <label htmlFor="agree-info">     ()</label>
            </div>
            {/*      */}
            <span className="agreement-link" onClick={handleOpenTermsPopup}>
               
            </span>
          </div>
        </div>

        {/* --- :      --- */}
        <div className="order-summary-sidebar">
          <div className="summary-box">
            <h3>  </h3>
            {/*   */}
            <div className="summary-row">
              <span> </span>
              <span>{orderSubtotal.toLocaleString()}</span>
            </div>
            <div className="summary-row">
              <span></span>
              <span>{shippingFee.toLocaleString()}</span>
            </div>
            
            {/*     */}
            {pointsToUse > 0 && (
              <div className="summary-row discount">
                <span> </span>
                <span>- {pointsToUse.toLocaleString()}</span>
              </div>
            )}
            
            {/*    */}
            <div className="summary-total">
              <span>  </span>
              <span>{finalAmount.toLocaleString()}</span>
            </div>
            
            {/*    (   ) */}
            <button 
              type="button" 
              className="btn-primary"
              onClick={handlePaymentSubmit}
            >
              {finalAmount.toLocaleString()} 
            </button>
            {/*    (   ) */}
            <button 
              type="button" 
              className="btn-secondary"
              onClick={() => navigate('/order')}
            >
               
            </button>
            {/*     */}
            <p className="summary-note">
                  /   .
                   .
            </p>
          </div>
        </div>
      </div>

      {/* ---    --- */}
      {isTermsPopupOpen && <TermsPopup onClose={handleCloseTermsPopup} />}
    </div>
  );
}

export default PaymentPage;