import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Slider from "react-slick";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import '../css/BestSeller.css';

import ProductCard from '../components/ProductCard';
import { fetchWithAuth, isLoggedIn, getStoredMember } from '../utils/api'; //  

//  ->   
const skinTypeMap = {
    dry: '',
    oily: '',
    combination: '',
    sensitive: '',
    all: ' '
};

function MultipleItems() {
    const navigate = useNavigate();
    const [products, setProducts] = useState([]);

    const settings = {
        infinite: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 3000,
        arrows: true,
        //   (     )
        responsive: [
            {
                breakpoint: 1024,
                settings: { slidesToShow: 3 }
            },
            {
                breakpoint: 600,
                settings: { slidesToShow: 2 }
            },
            {
                breakpoint: 480,
                settings: { slidesToShow: 1 }
            }
        ]
    };

    //    ' ' 10 
    useEffect(() => {
        const fetchBestSellers = async () => {
            try {
                // sort=popularity, size=10 
                const response = await fetch('http://13.231.28.89:18080/api/products?page=1&size=10&sort=popularity');
                if (!response.ok) throw new Error("  ");

                const data = await response.json();

                //     
                const validProducts = (data.content || []).filter(p => p.status !== '');
                setProducts(validProducts);

            } catch (error) {
                console.error("    :", error);
            }
        };

        fetchBestSellers();
    }, []);

    //      
    const handleProductClick = (prdNo) => {
        navigate(`/products/${prdNo}`);
    };

    //    (ProductListPage  )
    const handleAddToCart = async (e, product) => {
        e.stopPropagation(); //   ( ) 

        //  
        if (product.status === '') {
            alert("  .");
            return;
        }

        if (!isLoggedIn()) {
            alert('  .');
            navigate('/login');
            return;
        }

        const member = getStoredMember();
        if (!product.defaultOptionNo) {
            alert('   .   .');
            navigate(`/products/${product.prdNo}`);
            return;
        }

        try {
            const response = await fetchWithAuth('/coco/members/cart/items', {
                method: 'POST',
                body: JSON.stringify({
                    memNo: member.memNo,
                    optionNo: product.defaultOptionNo,
                    cartQty: 1
                })
            });

            if (response.ok) {
                if (window.confirm(' . \n ?')) {
                    navigate('/cart');
                }
            } else {
                alert('  .');
            }
        } catch (error) {
            console.error(error);
            alert(' .');
        }
    };

    //    
    if (products.length === 0) {
        return <div style={{ textAlign: 'center', padding: '50px', color: '#888' }}>   .</div>;
    }

    return (
        <div className="slider-container">
            <Slider {...settings}>
                {products.map((p, index) => {

                    //     
                    const safeStatus = p.status ? p.status.trim() : "";
                    const isSoldOut = safeStatus === '' || safeStatus === 'SOLD_OUT';

                    //    
                    const koreanSkinTypes = p.skinTypes
                        ? p.skinTypes.map(type => skinTypeMap[type] || type)
                        : [];

                    return (
                        <div key={p.prdNo} style={{ padding: '10px', position: 'relative' }}> {/*     */}

                            {/* ProductCard */}
                            <ProductCard
                                name={p.prdName}
                                productSkinType={koreanSkinTypes}
                                price={p.prdPrice.toLocaleString()}
                                image={p.imageUrl}
                                star_avg={p.averageRating}
                                reviewCount={p.reviewCount}
                                //  onClick    ( )
                                onClick={isSoldOut ? undefined : () => handleProductClick(p.prdNo)}
                                onAddToCart={(e) => handleAddToCart(e, p)}
                                isSoldOut={isSoldOut}
                                rank={index + 1}
                                // ProductCard     style prop  
                                style={{ cursor: isSoldOut ? 'default' : 'pointer' }}
                            />
                        </div>
                    );
                })}
            </Slider>
        </div>
    );
}

export default MultipleItems;