import React, { useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import '../../css/OrderPage.css';
import { useOrder } from '../OrderContext';
import axios from 'axios';

//       (  2)
function OrderPage() {
  const navigate = useNavigate();
  const location = useLocation();

  //   (OrderContext)   state   .
  const {
    lastName, setLastName,
    firstName, setFirstName,
    phone, setPhone,
    postcode, setPostcode,
    address, setAddress,
    addressDetail, setAddressDetail,
    deliveryMessage, setDeliveryMessage,
    shippingFee,
    orderSubtotal,  //    (  )
    setOrderSubtotal, //   
    setShippingFee,   // 
    setOrderItems,     //   
    setPointsToUse
  } = useOrder();

  //           
  useEffect(() => {
    setLastName('');
    setFirstName('');
    setPhone('');
    setPostcode('');
    setAddress('');
    setAddressDetail('');
    setDeliveryMessage('');

    //     
    if (setPointsToUse) setPointsToUse(0);
  }, [
    setLastName, 
    setFirstName, 
    setPhone, 
    setPostcode, 
    setAddress, 
    setAddressDetail, 
    setDeliveryMessage, 
    setPointsToUse
  ]); //  :       

  //      Context  
  useEffect(() => {
    if (location.state) {
      const { orderItems, orderSubtotal, shippingFee } = location.state;

      if (orderItems && setOrderItems) {
        setOrderItems(orderItems);
      }
      if (orderSubtotal && setOrderSubtotal) {
        setOrderSubtotal(orderSubtotal);
      }
      if (shippingFee && setShippingFee) {
        setShippingFee(shippingFee);
      }
    }

  }, [location.state, setOrderItems, setOrderSubtotal, setShippingFee]);

  // Daum Postcode API    
  useEffect(() => {
    // window.daum  Postcode      .
    if (!window.daum || !window.daum.Postcode) {
      console.error("Daum Postcode API   .");
    }
  }, []); //      


  //       Context state  
  const handleAddressSelect = (selectedPostcode, selectedAddress) => {
    setPostcode(selectedPostcode);
    setAddress(selectedAddress);
  };

  // Daum Postcode API   
  const handleOpenPopup = () => {
    if (window.daum && window.daum.Postcode) {
      new window.daum.Postcode({
        oncomplete: function (data) {
          handleAddressSelect(data.zonecode, data.address);
        }
      }).open();
    } else {
      alert("  API  .    .");
    }
  };


  const handleLoadMyInfo = async () => {

    // 1.    
    const token = localStorage.getItem('token');//   
    if (!token) {
      alert("  .");
      // ( ) navigate('/login'); 
      return;
    }

    try {
      // 2.  API  (  )
      // Authorization    .
      const response = await axios.get('http://13.231.28.89:18080/api/member/me', {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      const member = response.data; //     (Entity)
      console.log("  :", member);

      // 3.     (Context  )

      //  : DB  (memName) ,  / .
      if (member.memName && member.memName.length > 1) {
        setLastName(member.memName.substring(0, 1)); //   
        setFirstName(member.memName.substring(1));   //  
      } else {
        setLastName(member.memName || "");
        setFirstName("");
      }

      //    (     )
      setPhone(member.memHp || "");
      setPostcode(member.memZipcode || "");
      setAddress(member.memAddress1 || "");
      setAddressDetail(member.memAddress2 || "");



    } catch (error) {
      console.error("   :", error);
      alert("  . (  )");
    }
  };


  //         
  const handleToPayment = () => {
    if (!lastName || !firstName || !phone || !postcode || !address || !addressDetail) {
      alert('   .');
    } else {
      //      
      navigate('/payment');
    }
  };

  //     
  const freeShippingThreshold = 30000;
  const isShippingFree = orderSubtotal >= freeShippingThreshold;

  return (
    <div className="order-page">


      <h1></h1>

      <div className="order-content-wrapper">
        {/* --- 2. :     --- */}
        <div className="shipping-details">

          {/*         */}
          <div className="section-header">
            <h2> </h2>
            <button
              type="button"
              className="btn-load-info"
              onClick={handleLoadMyInfo} // []   
            >
                
            </button>
          </div>
           {/*      */}
          <p className="required-note">
            *    .
          </p>

          <form className="shipping-form">
            {/* /   */}
            <div className="form-group-half">
              <div className="form-group">
                <label htmlFor="last-name"> *</label>
                <input type="text" id="last-name" placeholder=""
                  value={lastName} onChange={(e) => setLastName(e.target.value)} />
              </div>
              <div className="form-group">
                <label htmlFor="first-name"> *</label>
                <input type="text" id="first-name" placeholder=""
                  value={firstName} onChange={(e) => setFirstName(e.target.value)} />
              </div>
            </div>
            {/*     */}
            <div className="form-group">
              <label htmlFor="phone">  *</label>
              <input type="text" id="phone" placeholder="010-1234-5678"
                value={phone} onChange={(e) => setPhone(e.target.value)} />
            </div>
            {/*       */}
            <div className="form-group form-group-with-button">
              <div className="form-group">
                <label htmlFor="postcode"> *</label>
                <input type="text" id="postcode" placeholder="12345"
                  value={postcode} readOnly /> {/*     */}
              </div>
              <button type="button" className="btn-outline" onClick={handleOpenPopup}>
                 
              </button>
            </div>
            {/*    ( ) */}
            <div className="form-group">
              <label htmlFor="address"> *</label>
              <input type="text" id="address" placeholder="   123"
                value={address} readOnly /> {/*     */}
            </div>
            {/*     */}
            <div className="form-group">
              <label htmlFor="address-detail">  *</label>
              <input type="text" id="address-detail" placeholder="456"
                value={addressDetail} onChange={(e) => setAddressDetail(e.target.value)} />
            </div>
            {/*     */}
            <div className="form-group">
              <label htmlFor="delivery-message"> </label>
              <input type="text" id="delivery-message" placeholder="   "
                value={deliveryMessage} onChange={(e) => setDeliveryMessage(e.target.value)} />
            </div>
          </form>

          {/*    */}
          <h2> </h2>
          <div className="shipping-method-box">
            <div className="method-title">
              <strong> </strong>
              {/* /    */}
              <span className={isShippingFree ? 'free' : 'fee'}>
                {isShippingFree ? '' : `${shippingFee.toLocaleString()}`}
              </span>
            </div>
            <p>  2-3 </p>
            {/*         */}
            {isShippingFree ? (
              <div className="shipping-highlight">   </div>
            ) : (
              <div className="shipping-warning">
                30,000    3,000 .
              </div>
            )}
          </div>
          {/*      */}
          <p className="shipping-fine-print">
            30,000    <br />
                    
          </p>
        </div>

        {/* --- 3. :       --- */}
        <div className="order-summary-sidebar">
          <div className="summary-box">
            <h3> </h3>
            <p>      .</p>
            {/*      */}
            <button type="button" className="btn-primary" onClick={handleToPayment}>
                
            </button>
            {/*      () */}
            <button type="button" className="btn-secondary" onClick={() => navigate("/cart")}> </button>
          </div>
        </div>
      </div>

    </div>
  );
}

export default OrderPage;