import React, { useState, useEffect } from 'react';
import { Navigate, Outlet } from 'react-router-dom';
import { isLoggedIn, getStoredMember, getCurrentMember } from '../utils/api';
import Forbidden from '../pages/error/Forbidden';

//      
const AdminProtectedRoute = () => {
  const [isChecking, setIsChecking] = useState(true);
  const [isAdmin, setIsAdmin] = useState(false);

  useEffect(() => {
    const checkAdminRole = async () => {
      //  
      if (!isLoggedIn()) {
        setIsChecking(false);
        return;
      }

      try {
        //     
        const memberData = await getCurrentMember();
        const userRole = memberData?.role || '';
        
        if (userRole === 'ADMIN' || userRole === 'admin') {
          setIsAdmin(true);
        } else {
          setIsAdmin(false);
        }
      } catch (error) {
        //     localStorage 
        console.error('  :', error);
        const memberData = getStoredMember();
        const userRole = memberData?.role || '';
        setIsAdmin(userRole === 'ADMIN' || userRole === 'admin');
      }
      
      setIsChecking(false);
    };

    checkAdminRole();
  }, []);

  if (isChecking) {
    return <div style={{ padding: '20px', textAlign: 'center' }}>  ...</div>;
  }

  //  
  if (!isLoggedIn()) {
    return <Navigate to="/login" replace />;
  }

  //   
  if (!isAdmin) {
    return <Forbidden />;
  }

  return <Outlet />;
};

export default AdminProtectedRoute;

