import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';
import Pagination from '../../components/admin/Pagination';
import Spinner from '../../components/admin/Spinner';
import '../../css/admin/AdminProductList.css';
import '../../css/admin/AdminComponents.css';

function AdminOrderList() {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [selectedOrder, setSelectedOrder] = useState(null); //   

  //   
  const [searchStatus, setSearchStatus] = useState('');
  const [searchTerm, setSearchTerm] = useState('');

  //      
  const statusMap = {
    PENDING: "",
    PAID: "",
    PREPARING: "",
    SHIPPING: "",
    DELIVERED: "",
    CANCELLED: "",
  };

  //     
  const getStatusColor = (status) => {
    switch (status) {
      case 'CANCELLED': 
        return '#dc3545'; //  ()
      case 'DELIVERED': 
        return '#28a745'; //  ()
      case 'PAID': 
        return '#007bff'; //  ( ->   )
      case 'PREPARING':
      case 'SHIPPING': 
        return '#fd7e14'; //  ()
      case 'PENDING': 
        return '#6c757d'; //  ()
      default: 
        return '#333';    //  
    }
  };

  //   
  const statusOptions = Object.keys(statusMap);

  const fetchOrders = useCallback(async (resetPage = false) => {
    setLoading(true);
    try {
      const token = localStorage.getItem('token');
      const params = {
        page: resetPage ? 1 : currentPage,
        size: 10,
        status: searchStatus || null,     //   null
        searchTerm: searchTerm || null    //   null
      };
      const response = await axios.get(`http://13.231.28.89:18080/api/admin/orders?page=${currentPage}&size=10`, {
        headers: { Authorization: `Bearer ${token}` },
        params: params
      });

      setOrders(response.data.content);
      setTotalPages(response.data.totalPages);
      if (resetPage) setCurrentPage(1);

    } catch (error) {
      console.error(error);
      toast.error("   .");
    } finally {
      setLoading(false);
    }
  }, [currentPage, searchStatus, searchTerm]);

  // useEffect  fetchOrders 
  useEffect(() => {
    fetchOrders(); 
  }, [fetchOrders]);

  //   
  const handleSearch = () => {
    fetchOrders(true); //   1 
  };

  //   
  const handleKeyDown = (e) => {
    if (e.key === 'Enter') handleSearch();
  };

  const handleStatusChange = async (orderNo, newStatus) => {
    if (!window.confirm(` '${statusMap[newStatus]}'() ?`)) return;

    try {
      const token = localStorage.getItem('token');
      await axios.patch(`http://13.231.28.89:18080/api/admin/orders/${orderNo}/status`,
        { status: newStatus },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      toast.success(" .");

      //     , UI 
      setOrders(prev => prev.map(order =>
        order.orderNo === orderNo ? { ...order, status: newStatus } : order
      ));

    } catch (error) {
      toast.error("  ");
    }
  };

  //  
  const openModal = (order) => {
    setSelectedOrder(order);
  };

  //  
  const closeModal = () => {
    setSelectedOrder(null);
  };

  return (
    <div className="admin-page-container">
      <h2 className="page-title"> </h2>

      <div className="admin-content-card">
        <div className="content-header">
          <h3>  </h3>
          <div className="header-actions">
            <button className="btn-refresh" onClick={() => fetchOrders(false)}> </button>
          </div>
        </div>

        {/*     */}
        <div className="filter-container" style={{marginBottom: '20px', display: 'flex', gap: '10px'}}>
          <select 
            className="filter-select"
            value={searchStatus}
            onChange={(e) => setSearchStatus(e.target.value)}
          >
            <option value=""> </option>
            {statusOptions.map(key => (
              <option key={key} value={key}>{statusMap[key]}</option>
            ))}
          </select>

          <input 
            type="text" 
            className="search-input"
            placeholder="   " 
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            onKeyDown={handleKeyDown}
          />

          <button 
            className="btn-search"
            onClick={handleSearch}
          >
            
          </button>
        </div>

        <div className="table-wrapper">
          <table className="admin-table">
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {loading ? (
                <tr><td colSpan="6" className="loading-cell"><Spinner /></td></tr>
              ) : orders.length > 0 ? (
                orders.map((order) => (
                  <tr key={order.orderNo}>
                    <td onClick={() => openModal(order)} style={{ cursor: 'pointer', textDecoration: 'underline' }}>
                      {order.orderNo}
                    </td>
                    <td>{order.orderDate}</td>
                    <td>{order.recipientName}</td>
                    <td>
                      {order.items && order.items.length > 0
                        ? `${order.items[0].productName} ${order.items.length > 1 ? ` ${order.items.length - 1}` : ''}`
                        : '-'}
                    </td>
                    <td>{order.totalPrice.toLocaleString()}</td>
                    <td>
                      <select
                        className="status-select"
                        value={order.status}
                        onChange={(e) => handleStatusChange(order.orderNo, e.target.value)}
                        style={{
                          padding: '6px',
                          borderRadius: '4px',
                          border: '1px solid #ddd',
                          fontWeight: 'bold',
                          color: getStatusColor(order.status)
                        }}
                      >
                        {statusOptions.map(key => (
                          <option key={key} value={key}>
                            {statusMap[key]}
                          </option>
                        ))}
                      </select>
                    </td>
                  </tr>
                ))
              ) : (
                <tr><td colSpan="6" className="empty-cell">  .</td></tr>
              )}
            </tbody>
          </table>
        </div>

        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={setCurrentPage}
        />
      </div>

      {/* ---    --- */}
      {selectedOrder && (
        <div className="modal-overlay" onClick={closeModal}>
          <div className="modal-content" onClick={e => e.stopPropagation()}>
            <div className="modal-header">
              <h3>   (No. {selectedOrder.orderNo})</h3>
              <button className="btn-close" onClick={closeModal}></button>
            </div>

            <div className="modal-body">
              <div className="info-group">
                <h4>  </h4>
                <p><strong>:</strong> {selectedOrder.recipientName}</p>
                <p><strong>:</strong> {selectedOrder.recipientPhone}</p>
                <p><strong>:</strong> [{selectedOrder.orderZipcode}] {selectedOrder.orderAddress1} {selectedOrder.orderAddress2}</p>
                <p><strong>:</strong> {selectedOrder.deliveryMessage || '-'}</p>
              </div>

              <div className="info-group">
                <h4>   </h4>
                <table className="modal-table">
                  <thead>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    {selectedOrder.items.map((item, idx) => (
                      <tr key={idx}>
                        <td>{item.productName}</td>
                        <td>{item.optionName || ''}</td>
                        <td>{item.qty}</td>
                        <td>{item.price.toLocaleString()}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>

            <div className="modal-footer">
              <button className="btn-secondary" onClick={closeModal}></button>
            </div>
          </div>
        </div>
      )}

    </div>
  );
}

export default AdminOrderList;